import React, { Component } from "react";
import "../../../App.css";

class BaleBusitngForm extends Component {
  render() {
    return (
      <div>
        <form>
          <div className="form-row to-seed-top-row-color">
            <div className="form-group col-lg-5 ">
              <div className="row">
                <div className="col">
                  <div className="to-purchase-title">Barcode</div>

                  <input
                    type="text"
                    className="form-control"
                    id="inputSalesOrder"
                    placeholder="11QQAZ33456"
                  />
                </div>

                <div className="col">
                  <div className="to-purchase-title">Lot # Per Pallet</div>

                  <input
                    type="text"
                    className="form-control"
                    id="inputDiscounts"
                    placeholder="1234567890"
                  />
                </div>
              </div>
            </div>

            <div className="form-group col-lg-3 ">
              <div className="row">
                <div className="col">
                  <div className="to-purchase-title">PH Level</div>

                  <input
                    type="text"
                    className="form-control"
                    id="inputDiscounts"
                    placeholder=""
                  />
                </div>
                <div className="col">
                  <div className="to-purchase-title-small">
                    <p>
                      Vendor <br /> PH Level
                    </p>
                  </div>

                  <input
                    type="text"
                    className="form-control"
                    id="inputDiscounts"
                    placeholder=""
                  />
                </div>
              </div>
            </div>

            <div className="form-group col-lg-4">
              <div className="row">
                <div className="col">
                  <div className="to-purchase-title">Moisture Level</div>
                  <input
                    type="text"
                    className="form-control"
                    id="inputDiscounts"
                    placeholder=""
                  />
                </div>
                <div className="col">
                  <div className="to-purchase-title-small">
                    <p>
                      Vendor <br /> Moisture Level
                    </p>
                  </div>
                  <input
                    type="text"
                    className="form-control"
                    id="inputDiscounts"
                    placeholder=""
                  />
                </div>
              </div>
            </div>
          </div>
        </form>
      </div>
    );
  }
}

export default BaleBusitngForm;
