import React, { Component } from "react";
import { Link } from "react-router-dom";
import Header from "../../components/Header";
import DirtProductionMain3Filter from "./Components/DirtProductionMain3Filter";

class DirtProductionMain3 extends Component {
  componentDidMount() {
    document.title = "Dirt Production";
  }
  render() {
    return (
      <div>
        <Header pageTitle="Dirt Production" headerSetting="to-hide" />
        <DirtProductionMain3Filter />

        <div className="container-fluid">
          <div className="row">
            <div className="col-12">
              <div className="text-center es_verticaCenter">
                <h1 className="es_dirtText">Please choose an option</h1>{" "}
              </div>

              <div className="DirtProductionButtonArea">
                <Link
                  className="btn seeding-dirt-btn"
                  to="/seeding/bale_busting"
                >
                  Bale Busting
                </Link>
                <Link
                  className="btn seeding-dirt-btn"
                  to="/seeding/dirt_production2"
                >
                  Tray Filling
                </Link>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default DirtProductionMain3;
