import React, { Component } from "react";
import Header from "../../../components/Header";
import CopyRightsText from "../../../components/CopyRightsText";

class HowToSpray extends Component {
  componentDidMount() {
    document.title = "How To Spray";
  }
  render() {
    return (
      <div>
        <Header pageTitle="How To Spray" headerSetting="to-hide" />
        <div className="to-filter-bg">
          <div className="row">
            <div className="col-md-3 offset-md-9  text-right">
              <div className="input-group to-input-tb">
                <input className="form-control py-2" placeholder="Chemical A" />
                <span className="input-group-append">
                  <button
                    className="btn btn-outline-secondary to-filter-btn"
                    type="button"
                  >
                    <i className="fa fa-search to-icon-color"></i>
                  </button>
                </span>
              </div>
            </div>
          </div>
        </div>

        <div className="HowToSprayTable">
          <table className="table table-borderless HowToSpray">
            <tbody>
              <tr className="HowToSprayHeader">
                <td>Chemical name</td>
                <td>Title</td>
                <td>PDF</td>
                <td>Video</td>
              </tr>
              <tr className="HowToSprayRow2">
                <td>Chemical name</td>
                <td>How to Spray Chemical A</td>
                <td>
                  <div className="how-to-spray-td">
                    <a href="#/">
                      <img
                        src={require("../../../assets/icons/icon-pdf.png")}
                      />
                    </a>
                  </div>
                </td>
                <td>
                  <div className="how-to-spray-td">
                    <a href="#/">
                      <img src={require("../../../assets/icons/icon-yt.png")} />
                    </a>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
        <div className="HowtoSprayPadding"></div>
        <CopyRightsText />
      </div>
    );
  }
}

export default HowToSpray;
