import React, { Component } from "react";
import "../../../App.css";
class BallWeeklyBottom extends Component {
  render() {
    return (
      <div>
        <section className="button-area">
          <div className="container-fluid ">
            <div className="row">
              <div className="col-xl-6 ">
                <button type="button" className="btn  btn-lg to-weekly-button">
                  CHANGE ORDER DATE
                </button>
                <button type="button" className="btn  btn-lg to-weekly-button">
                  ADD SUBSTITUTE
                </button>
                <button type="button" className="btn  btn-lg to-weekly-button">
                  CHANGE VENDOR
                </button>
              </div>

              <div className="col-xl-6 text-right mt-2 d-flex  justify-content-md-center">
                <button type="button" className="btn  btn-lg to-button-default">
                  Save
                </button>
                <button type="button" className="btn  btn-lg to-button-default">
                  Email
                </button>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

export default BallWeeklyBottom;
