import React, { Component } from "react";
import Header from "../../components/Header";
import { Link } from "react-router-dom";
import CopyRightsText from "../../components/CopyRightsText";

class Training extends Component {
  componentDidMount() {
    document.title = "Training";
  }
  render() {
    return (
      <div>
        <Header pageTitle="Training" headerSetting="to-hide" />

        <div className="container-fluid">
          <div className="row">
            <div className="col-12">
              <div className="TrainingArea">
                <div className="TrainingButtons">
                  <Link
                    type="button"
                    className="btn growing-training-btn"
                    to="/growing/how_to_spray"
                  >
                    How to Spray
                  </Link>
                  <Link
                    type="button"
                    className="btn growing-training-btn"
                    to="#/"
                  >
                    How to Mix
                  </Link>
                </div>
              </div>
            </div>
          </div>
        </div>
        <CopyRightsText />
      </div>
    );
  }
}

export default Training;
