import React, { Component } from "react";
import { Link } from "react-router-dom";
import "../App.css";

class ChooseModule extends Component {
  state = {
    showModules: false
  };
  OnShowClick = () => {
    this.setState({ showModules: !this.state.showModules });
  };
  render() {
    const { showModules } = this.state;
    return (
      <div>
        <div className="row">
          <div className="col-xl-6 offset-md-3 p-t-100">
            <form className="text-center">
              <div className="form-group new-form">
                <input
                  type="text"
                  list="browsers"
                  className="form-control"
                  id="autocomplete"
                  placeholder="Search Modules"
                />
                <div className="search-menu-icon">
                  <a onClick={this.OnShowClick}>
                    <img src={require("../assets/icons/menu.png")} alt="menu" />
                  </a>
                </div>
              </div>
            </form>
          </div>
        </div>
        {showModules ? (
          <section className="chooseModule">
            <div className="container">
              <div className="row">
                <div className="col-xl-6">
                  <div className="chooseModuleHeader">
                    <p>Choose a module</p>
                  </div>
                </div>
                <div className="col-xl-6">
                  <div className="chooseModuleIcon text-right">
                    <ul>
                      <li>
                        <Link to="/dashboard">
                          <img
                            src={require("../assets/icons/home-white.png")}
                            alt=""
                          />
                        </Link>
                      </li>
                      <li
                        onClick={this.OnShowClick}
                        className="iconModuleClose"
                      >
                        <img
                          src={require("../assets/icons/exit-white.png")}
                          alt=""
                        />
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
              <div className="row">
                <div className="col-xl-3">
                  <div className="chooseModule-item-box">
                    <img
                      src={require("../assets/icons/icon_seeding_main.png")}
                      alt=""
                    />
                    <span>
                      <Link to="/seeding/dashboard">Seeding</Link>
                    </span>
                  </div>
                </div>
                <div className="col-xl-3">
                  <div className="chooseModule-item-box">
                    <img
                      src={require("../assets/icons/icon_patching.svg")}
                      alt=""
                    />
                    <span>
                      <Link to="/patching/dashboard">Patching</Link>
                    </span>
                  </div>
                </div>
                <div className="col-xl-3">
                  <div className="chooseModule-item-box">
                    <img
                      src={require("../assets/icons/icon_cutting.svg")}
                      alt=""
                    />
                    <span>
                      <Link to="/cuttings/dashboard">Cuttings</Link>
                    </span>
                  </div>
                </div>
                <div className="col-xl-3">
                  <div className="chooseModule-item-box">
                    <img
                      src={require("../assets/icons/icon_transplanting.svg")}
                      alt=""
                    />
                    <span>
                      <a href="#/">Transplanting</a>
                    </span>
                  </div>
                </div>
              </div>
              <div className="row">
                <div className="col-xl-3">
                  <div className="chooseModule-item-box">
                    <img
                      src={require("../assets/icons/icon_logistics.svg")}
                      alt=""
                    />
                    <span>
                      <Link to="/logistics/dashboard">Logistics</Link>
                    </span>
                  </div>
                </div>
                <div className="col-xl-3">
                  <div className="chooseModule-item-box">
                    <img
                      src={require("../assets/icons/icon_purchasing_main.svg")}
                      alt=""
                    />
                    <span>
                      <Link to="/purchasing/dashboard">Purchase Order</Link>
                    </span>
                  </div>
                </div>
                <div className="col-xl-3">
                  <div className="chooseModule-item-box">
                    <img src={require("../assets/icons/tint.png")} alt="" />
                    <span>
                      <a href="#/">New Item</a>
                    </span>
                  </div>
                </div>
                <div className="col-xl-3">
                  <div className="chooseModule-item-box">
                    <img
                      src={require("../assets/icons/icon_quality.svg")}
                      alt=""
                    />
                    <span>
                      <a href="#/">Quality</a>
                    </span>
                  </div>
                </div>
              </div>
              <div className="row">
                <div className="col-xl-3">
                  <div className="chooseModule-item-box">
                    <img
                      src={require("../assets/icons/icon_greenhousespace.svg")}
                      alt=""
                    />
                    <span>
                      <Link to="/space_allocation/dashboard">
                        Green House Allocation
                      </Link>
                    </span>
                  </div>
                </div>
                <div className="col-xl-3">
                  <div className="chooseModule-item-box">
                    <img
                      src={require("../assets/icons/icon_growing.svg")}
                      alt=""
                    />
                    <span>
                      <Link to="/growing/dashboard">Growing</Link>
                    </span>
                  </div>
                </div>
                <div className="col-xl-3">
                  <div className="chooseModule-item-box">
                    <img
                      src={require("../assets/icons/icon_costing.svg")}
                      alt=""
                    />
                    <span>
                      <a href="#/">Costing</a>
                    </span>
                  </div>
                </div>
                <div className="col-xl-3">
                  <div className="chooseModule-item-box">
                    <img src={require("../assets/icons/icon_rm.svg")} alt="" />
                    <span>
                      <a href="#/">Relationship Management</a>
                    </span>
                  </div>
                </div>
              </div>
              <div className="row">
                <div className="col-xl-3">
                  <div className="chooseModule-item-box">
                    <img
                      src={require("../assets/icons/icon_assetmanagement.svg")}
                      alt=""
                    />
                    <span>
                      <a href="#/">Asset Management</a>
                    </span>
                  </div>
                </div>
                <div className="col-xl-3">
                  <div className="chooseModule-item-box">
                    <img
                      src={require("../assets/icons/icon_maintaintruck.svg")}
                      alt=""
                    />
                    <span>
                      <a href="#/">Maintenance Trucking</a>
                    </span>
                  </div>
                </div>
                <div className="col-xl-3">
                  <div className="chooseModule-item-box">
                    <img
                      src={require("../assets/icons/icon_maintainfacility.svg")}
                      alt=""
                    />
                    <span>
                      <a href="#/">Maintaenance Facility</a>
                    </span>
                  </div>
                </div>
                <div className="col-xl-3">
                  <div className="chooseModule-item-box">
                    <img
                      src={require("../assets/icons/icon_timeclock.svg")}
                      alt=""
                    />
                    <span>
                      <a href="#/">Time Clock</a>
                    </span>
                  </div>
                </div>
              </div>
              <div className="row">
                <div className="col-xl-3">
                  <div className="chooseModule-item-box">
                    <img src={require("../assets/icons/icon_hr.svg")} alt="" />
                    <span>
                      <a href="#/">Human Resources</a>
                    </span>
                  </div>
                </div>
                <div className="col-xl-3">
                  <div className="chooseModule-item-box">
                    <img
                      src={require("../assets/icons/icon_securitypermissions.svg")}
                      alt=""
                    />
                    <span>
                      <a href="#/">Security permissions</a>
                    </span>
                  </div>
                </div>
                <div className="col-xl-3">
                  <div className="chooseModule-item-box">
                    <img
                      src={require("../assets/icons/icon_reporting.svg")}
                      alt=""
                    />
                    <span>
                      <a href="#/">Reporting</a>
                    </span>
                  </div>
                </div>
                <div className="col-xl-3">
                  <div className="chooseModule-item-box">
                    <img
                      src={require("../assets/icons/icon_timelineestimated.svg")}
                      alt=""
                    />
                    <span>
                      <a href="#/">Time Line Estimated</a>
                    </span>
                  </div>
                </div>
              </div>
            </div>
          </section>
        ) : null}
      </div>
    );
  }
}

export default ChooseModule;
