import React, { Component } from "react";
import Header from "../../components/Header";
import SearchPurchaseOrderHeader from "./Components/SearchPurchaseOrderHeader";
import SearchPurchaseOrderTable from "./Components/SearchPurchaseOrderTable";
import SearchOrderNotificatoins from "./Components/SearchOrderNotificatoins";
import CopyRightsText from "../../components/CopyRightsText";

class SearchPurchaseOrder extends Component {
  componentDidMount() {
    document.title = "Search Purchase Order";
  }
  render() {
    return (
      <div>
        <Header pageTitle="Search Purchase Order" />
        <SearchPurchaseOrderHeader />
        <SearchPurchaseOrderTable />
        <SearchOrderNotificatoins />
        <CopyRightsText />
      </div>
    );
  }
}

export default SearchPurchaseOrder;
