import React, { Component } from "react";
import Header from "../../components/Header";
import CuttingsLogoArea from "./Components/CuttingsLogoArea";
import CuttingsSubModules from "./Components/CuttingsSubModules";
import CopyRightsText from "../../components/CopyRightsText";
import ChooseModule from "../../components/ChooseModule";

class CuttingsDashboard extends Component {
  componentDidMount() {
    document.title = "Cuttings Dashboard";
  }
  render() {
    return (
      <div className="main-bg">
        <Header pageTitle="Cuttings Prototype" />
        <div className="container">
          <CuttingsLogoArea />
          <ChooseModule />
          <CuttingsSubModules />
          <CopyRightsText />
        </div>
      </div>
    );
  }
}

export default CuttingsDashboard;
