import React, { Component } from "react";
import { Link } from "react-router-dom";
class PurchasingSubModules extends Component {
  render() {
    return (
      <div>
        <div className="row">
          <div className="col-xl-12">
            <div className="sub-module-title text-center">
              <h3>Purchasing Modules</h3>
            </div>
          </div>
        </div>
        <div className="row recent-used-item">
          <div className="col-sm-3 offset-1 ">
            <div className="recent-item-box text-center">
              <img
                src={require("../../../assets/icons/icon_purchasing_newpo.png")}
                alt=""
              />
              <span>
                <Link to="/purchasing/new_purchase_order">New PO</Link>
              </span>
            </div>
          </div>
          <div className="col-sm-3 ">
            <div className="recent-item-box text-center">
              <img
                src={require("../../../assets/icons/icon_purchasing_seachpo.png")}
                alt=""
              />
              <span>
                <Link to="/purchasing/search_purchase_order">Search PO's</Link>
              </span>
            </div>
          </div>
          <div className="col-sm-3 ">
            <div className="recent-item-box text-center">
              <img src={require("../../../assets/icons/audit.png")} alt="" />
              <span>
                <a href="#/">Reports</a>
              </span>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default PurchasingSubModules;
