import React, { Component } from "react";

class PatchingCombifixTopBar extends Component {
  render() {
    return (
      <div>
        <div className="to-filter-bg">
          <div className="row">
            <div className="col-md-2 col-sm-12">
              <div className="to-filter-text-patching">
                {this.props.patchingTitle}
              </div>
            </div>
            <div className="col-md-3  ">
              <div className={` ${this.props.combifixbutton}`}>
                <button
                  style={{ marginTop: ".6rem" }}
                  type="button"
                  className="patching-combifix-btn"
                >
                  Merge With Combifix
                </button>
              </div>
            </div>

            <div className="col-md-1 mt-2 offset-md-3">
              <div className="patching-box">
                <p>Work Log</p>
              </div>
            </div>

            <div className="col-3 text-right">
              <div className="patching-button-top">
                <button
                  style={{ marginTop: ".6rem" }}
                  className="patching-combifix-edit-btn"
                >
                  Edit
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default PatchingCombifixTopBar;
