import React, { Component } from "react";
import Header from "../../components/Header";
import SearchFilterArea from "../../components/SearchFilterArea";
import ShortagesExtrasTable from "./Components/ShortagesExtrasTable";
import CopyRightsText from "../../components/CopyRightsText";

class ShortageExtras extends Component {
  componentDidMount() {
    document.title = "Shortage & Extras";
  }
  render() {
    return (
      <div>
        <Header pageTitle="Shortage & Extras" />
        <SearchFilterArea placeholderText="Search Filter Area" />
        <ShortagesExtrasTable />
        <CopyRightsText />
      </div>
    );
  }
}

export default ShortageExtras;
