import React, { Component } from "react";
import "../App.css";

class LogoArea extends Component {
  render() {
    return (
      <div>
        <div className="row">
          <div className="col-xl-12">
            <div className="hero-img text-center">
              <img src={require("../assets/images/vbm_hero_img.png")} alt="" />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default LogoArea;
