import React, { Component } from "react";
import Header from "../../components/Header";
import SearchFilterArea from "../../components/SearchFilterArea";
import PatchingProductionTable from "./Components/PatchingProductionTable";
import CopyRightsText from "../../components/CopyRightsText";
import "../../App.css";
import PatchingProductionNotifications from "./Components/PatchingProductionNotifications";

class PatchingProduction extends Component {
  componentDidMount() {
    document.title = "Patching Production";
  }
  render() {
    return (
      <div>
        <Header pageTitle="Patching Production" />
        <SearchFilterArea placeholderText="Search Filter Area" />
        <PatchingProductionTable />
        <PatchingProductionNotifications />
        <CopyRightsText />
      </div>
    );
  }
}

export default PatchingProduction;
