import React, { Component, useEffect, useRef } from "react";
import "../App.css";
import { Link } from "react-router-dom";

//import components
import Logout from "./Logout";

class Header extends Component {
  constructor(props) {
    super(props);
    this.textInput = React.createRef();
    this.focus = this.focus.bind(this);
  }

  focus() {
    this.textInput.current.focus();
  }

  render() {
    return (
      <div>
        <nav className="navbar navbar-expand-lg navbar-light bg-light nav-custom-bg">
          <Link className="navbar-brand" to="/dashboard">
            <img
              style={{ width: "171px" }}
              src={require("../assets/images/vbm_revlg.png")}
              alt=""
            />
          </Link>

          <button
            className="navbar-toggler"
            type="button"
            data-toggle="collapse"
            data-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>

          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav mr-auto">
              <li className="nav-item dropdown nav-item-context nav-item-border">
                <a
                  className={`nav-link dropdown-toggle ${this.props.headerNotification}`}
                  href="#/"
                  id="navbarDropdown"
                  role="button"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                >
                  Greg Brewster<span className="header-notification">3</span>
                </a>
                <div
                  className="dropdown-menu myDropdown"
                  aria-labelledby="navbarDropdown"
                >
                  <p className="p-notification-text">My Notification</p>
                  <ul className="noti-ul text-center">
                    <li>
                      {" "}
                      <a className="" href="#/">
                        <img
                          src={require("../assets/icons/noti-icon.png")}
                          alt=""
                        />{" "}
                        Module Name
                        <span className="notification-item-icon">2</span>
                      </a>
                    </li>
                    <li>
                      {" "}
                      <a className="" href="#/">
                        <img
                          src={require("../assets/icons/noti-icon.png")}
                          alt=""
                        />{" "}
                        Module Name
                        <span className="notification-item-icon">1</span>
                      </a>
                    </li>
                  </ul>
                </div>
              </li>
              <li className="nav-item nav-item-context">
                <a className={`nav-link ${this.props.headerTitle}`}>
                  {this.props.pageTitle}
                </a>
              </li>
            </ul>
            <ul className="navbar-nav ml-auto">
              <li className="nav-item dropdown icon-cog myDropdown">
                <a
                  className={`nav-link dropdown-toggle ${this.props.headerSetting}`}
                  href="#/"
                  id="navbarDropdown"
                  role="button"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                >
                  <img
                    src={require("../assets/icons/setting-icon.png")}
                    alt=""
                  />
                </a>
                <div
                  className="dropdown-menu myDropdown-sub"
                  aria-labelledby="navbarDropdown"
                >
                  <Link className="dropdown-item" to="/users">
                    Users
                  </Link>
                  <Link className="dropdown-item" to="/user_roles">
                    Roles
                  </Link>
                  <Link className="dropdown-item" to="/permissions">
                    Permissions
                  </Link>
                  <Link className="dropdown-item" to="/error_logs">
                    Error Logs
                  </Link>
                  <Link className="dropdown-item" to="/audit_logs">
                    Audits
                  </Link>
                  <a
                    className="dropdown-item"
                    style={{ cursor: "pointer", color: "#fff" }}
                    data-toggle="modal"
                    data-target="#orderseedshowcolumns"
                  >
                    Column Picker
                  </a>
                  <Link className="dropdown-item" to="/user_settings">
                    Settings
                  </Link>
                </div>
              </li>
              <li className="nav-item nav-item-keyboard">
                <li className="nav-item ">
                  <input className="keboard-input" ref={this.textInput} />
                </li>
                <a
                  onClick={this.focus}
                  style={{ cursor: "pointer" }}
                  className="nav-link"
                >
                  <img
                    style={{ width: "53px" }}
                    src={require("../assets/icons/keyboard.png")}
                    alt="keyboard"
                  />
                </a>
              </li>

              <li className="nav-item nav-item-timer to-hide">
                <a href="#/" target="#" className="nav-link">
                  <img
                    style={{ width: "53px" }}
                    src={require("../assets/icons/icon_timer.png")}
                    alt="timer"
                  />
                </a>
              </li>

              <li className="nav-item">
                <a
                  className="nav-link"
                  href="#/"
                  target="#"
                  data-toggle="modal"
                  data-target="#logout"
                >
                  <img
                    style={{ width: "63px" }}
                    src={require("../assets/icons/logout.png")}
                    alt="logout"
                  />
                </a>
              </li>
            </ul>
          </div>
        </nav>
        <Logout />
      </div>
    );
  }
}

export default Header;
