import React, { Component } from "react";
import Header from "../../components/Header";
import PurchasingLogoArea from "./Components/PurchasingLogoArea";
import ChooseModule from "../../components/ChooseModule";
import PurchasingSubModules from "./Components/PurchasingSubModules";
import PurchasingDashboardNotifications from "./Components/PurchasingDashboardNotifications";
import CopyRightsText from "../../components/CopyRightsText";

class PurchasingDashboard extends Component {
  componentDidMount() {
    document.title = "Purchasing Dashboard";
  }

  render() {
    return (
      <div>
        <div className="main-bg">
          <Header pageTitle="Purchasing Dashboard" />

          <div className="container">
            <PurchasingLogoArea />
            <ChooseModule />
            <PurchasingSubModules />
            <PurchasingDashboardNotifications />
            <CopyRightsText />
          </div>
        </div>
      </div>
    );
  }
}

export default PurchasingDashboard;
