import React, { Component } from "react";
import Header from "../../components/Header";
import SearchFilterArea from "../../components/SearchFilterArea";
import RoutesTable from "./Components/RoutesTable";
import CopyRightsText from "../../components/CopyRightsText";

class Routes extends Component {
  componentDidMount() {
    document.title = "Routes";
  }

  render() {
    return (
      <div>
        <Header pageTitle="Routes" />
        <SearchFilterArea placeholderText="Search Filter Area" />
        <RoutesTable />
        <CopyRightsText />
      </div>
    );
  }
}

export default Routes;
