import React, { Component } from "react";
import { Link } from 'react-router-dom';

class BottomButtons extends Component {
  render() {
    return (
      <div>
        <section className="button-area">
          <div className="container">
            <div className="d-flex justify-content-lg-end justify-content-md-end justify-content-sm-start ">
              <Link to="/user_details" className="button-default to-button-default ">
                Edit
              </Link>

              <a href="#/" className="button-default to-button-default ">
                Delete
              </a>

              <Link to="/user_details" className="button-default to-button-default ">
                Add
              </Link>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

export default BottomButtons;
