import React, { Component } from "react";
import Header from "../../components/Header";
import CopyRightsText from "../../components/CopyRightsText";
import IncomingSeedTraysContent from "./Components/IncomingSeedTraysContent";

class IncomingSeedTrays extends Component {
  componentDidMount() {
    document.title = "Incoming Seed Trays";
  }
  render() {
    return (
      <div>
        <Header pageTitle="Incoming Seed Trays" />
        <IncomingSeedTraysContent />
        <CopyRightsText />
      </div>
    );
  }
}

export default IncomingSeedTrays;
