import React, { Component } from "react";

class SeedingLane1FormTable extends Component {
  render() {
    return (
      <div>
        <div className="row">
          <div className=" es_seedingLane es_seedingLaneForm col-md-6">
            <form>
              <div className="form-row">
                <div className="form-group col-md-6">
                  <label>Seed Package Barcode</label>
                  <input
                    type="text"
                    className="form-control es_seedingLaneInput"
                    placeholder="1234567890000000000"
                  />
                </div>
                <div className="form-group col-md-6">
                  <label>Lot #</label>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="1234567890"
                  />
                </div>
              </div>

              <div className="form-row">
                <div className="form-group col-md-6">
                  <label>Seq #</label>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="0987654321"
                  />
                </div>
                <div className="form-group col-md-6">
                  <label>Mat</label>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Mat"
                  />
                </div>
              </div>
              <div className="form-row">
                <div className="form-group col-md-6">
                  <label>Desc</label>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Desc"
                  />
                </div>
                <div className="form-group col-md-6">
                  <label>Type</label>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Type"
                  />
                </div>
              </div>

              <div className="form-row">
                <div className="form-group col-md-6">
                  <label>Qty</label>
                  <input type="text" className="form-control" placeholder="0" />
                </div>
                <div className="form-group col-md-6">
                  <label>Est Qty</label>
                  <input type="text" className="form-control" placeholder="0" />
                </div>
              </div>
            </form>
          </div>
          <div className="col-md-6">
            <ul className="nav nav-tabs to-nav">
              <li className="nav-item">
                <a
                  href="#Completed"
                  className="nav-link to-active active"
                  data-toggle="tab"
                >
                  Completed
                </a>
              </li>
              <li className="nav-item">
                <a href="#Future" class="nav-link to-active" data-toggle="tab">
                  Future
                </a>
              </li>
            </ul>
            <div className="tab-content">
              <div className="tab-pane fade show active" id="Completed">
                <div className=" es_bgTbody">
                  <table className="table table-sm to-white-color">
                    <thead>
                      <tr className="es_seedingLaneTableHeader ">
                        <th>Description</th>
                        <th>Item</th>
                        <th>Est Qty</th>
                      </tr>
                    </thead>
                  </table>
                </div>
              </div>
              <div className="tab-pane fade" id="Future">
                <div className="es_bgTbody">
                  <table className="table table-sm to-white-color">
                    <thead>
                      <tr className="es_seedingLaneTableHeader ">
                        <th>Lane</th>
                        <th>Species</th>
                        <th>Lot#</th>
                        <th>Buffer</th>
                        <th>Multi Sown#</th>
                        <th>Destination</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        <th>1</th>
                        <th>Macho Fem</th>
                        <th>123456</th>
                        <th>8</th>
                        <th>None</th>
                        <th>Greenhouse 5</th>
                      </tr>
                      <tr>
                        <th>1</th>
                        <th>Macho Fem</th>
                        <th>123456</th>
                        <th>8</th>
                        <th>None</th>
                        <th>Greenhouse 5</th>
                      </tr>
                      <tr>
                        <th>1</th>
                        <th>Macho Fem</th>
                        <th>123456</th>
                        <th>8</th>
                        <th>None</th>
                        <th>Greenhouse 5</th>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>

            <div className="row">
              <section className="es_buttonArea">
                <div>
                  <div className="BottomButtons">
                    <button type="button" className="btn   to-button-default">
                      Submit
                    </button>
                    <button type="button" className="btn   to-button-default">
                      Edit
                    </button>
                  </div>
                </div>
              </section>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default SeedingLane1FormTable;
