import React, { Component } from "react";
import Header from "../../components/Header";
import SpaceAllocationLogoArea from "./Components/SpaceAllocationLogoArea";
import SeedingChooseModule from "../Seeding/Components/SeedingChooseModule";
import CopyRightsText from "../../components/CopyRightsText";
import SpaceAllocationSubModules from "./Components/SpaceAllocationSubModules";
import SpaceAllocationDashboardNotifications from "./Components/SpaceAllocationDashboardNotifications";

class SpaceAllocationDashboard extends Component {
  componentDidMount() {
    document.title = "GreenHouse Space Allocation Dashboard";
  }

  render() {
    return (
      <div className="main-bg">
        <Header pageTitle="GreenHouse Space Allocation Dashboard" />

        <div className="container">
          <SpaceAllocationLogoArea />
          <SeedingChooseModule />
          <SpaceAllocationSubModules />
          <SpaceAllocationDashboardNotifications />
          <CopyRightsText />
        </div>
      </div>
    );
  }
}
export default SpaceAllocationDashboard;
