import React, { Component } from "react";
import "../../../App.css";
class TransplantingForm extends Component {
  render() {
    return (
      <div>
        <div className="container-fluid ">
          <form className="es_purchase-input">
            <div className="form-row">
              <div className="form-group col-md-3">
                <label className="to-title-f-p-size" for="inputTerms">
                  Items Sticked
                </label>
                <select className="form-control to-input-f-b-c to-classNameic">
                  <option defaultValue>...</option>
                  <option>...</option>
                </select>
              </div>
              <div className="form-group col-md-3">
                <div className="row">
                  <div className="col">
                    <label
                      className="to-title-f-p-size to-hide"
                      for="inputPurchaseOrder"
                    >
                      Qty
                    </label>
                    <input
                      type="text"
                      className="form-control to-input-f-b-c"
                      id="inputPurchaseOrder"
                      placeholder="qty"
                    />
                  </div>

                  <div className="col text-center">
                    <label
                      className="to-title-f-p-size to-label-top"
                      for="inputDept"
                    >
                      Transplanting
                    </label>
                  </div>
                </div>
              </div>
              <div className="form-group col-md-3">
                <label className="to-title-f-p-size" for="inputTerms">
                  Dropdown List
                </label>
                <select className="form-control to-input-f-b-c to-classNameic">
                  <option defaultValue>...</option>
                  <option>...</option>
                </select>
              </div>
              <div className="form-group col-md-3">
                <label className="to-title-f-p-size to-hide" for="inputCreator">
                  Qty
                </label>
                <input
                  type="text"
                  className="form-control to-input-f-b-c"
                  id="inputCreator"
                  placeholder="qty"
                />
              </div>
            </div>

            <div className="form-row">
              <div className="form-group col-md-4 offset-md-1">
                <div className="row">
                  <div className="col">
                    <label
                      className="to-title-f-p-size to-hide"
                      for="inputPurchaseOrder"
                    >
                      Location
                    </label>
                    <input
                      type="text"
                      className="form-control to-input-f-b-c"
                      id="inputPurchaseOrder"
                      placeholder="location"
                    />
                  </div>
                </div>
              </div>
              <div className="form-group text-center col-md-3 col-lg-2">
                <label
                  className="to-title-f-p-size to-label-top"
                  for="inputDept"
                >
                  Transplanting To
                </label>
              </div>
              <div className="form-group col-md-4">
                <label className="to-title-f-p-size to-hide" for="inputTerms">
                  Location
                </label>
                <input
                  type="text"
                  className="form-control to-input-f-b-c "
                  id="inputDept"
                  placeholder="location"
                />
              </div>
            </div>

            <button
              type="button"
              className="btn to-button-default"
              style={{ margin: "0 auto" }}
            >
              Submit
            </button>
          </form>
        </div>
      </div>
    );
  }
}

export default TransplantingForm;
