import React, { Component } from "react";

class GrowingLogoArea extends Component {
  render() {
    return (
      <div>
        <div>
          <div className="row">
            <div className="col-md-12 purchasingHeroArea">
              <div className="hero-img hero-img-small text-center">
                <img
                  src={require("../../../assets/images/vbm_logosm.png")}
                  alt=""
                />
                <div className="bob-image">
                  <img
                    src={require("../../../assets/images/bobsmarketlogo.gif")}
                    alt=""
                  />
                </div>
              </div>
              <div className="pur-dash-hero-area text-center">
                <h1>
                  <img
                    src={require("../../../assets/icons/growing-plant.png")}
                    alt=""
                  />{" "}
                  Growing
                </h1>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default GrowingLogoArea;
