import React, { Component } from "react";
import Header from "../../components/Header";

import SearchByMapArea from "./Components/SearchByMapArea";
import SearchByMapNotifications from "./Components/SearchByMapNotifications";

class SearchByMap extends Component {
  componentDidMount() {
    document.title = "Search By Map";
  }
  render() {
    return (
      <div>
        <Header pageTitle="Search By Map" />
        <SearchByMapArea />
        <SearchByMapNotifications />
      </div>
    );
  }
}

export default SearchByMap;
