import React, { Component } from "react";
import { Link } from "react-router-dom";

export default class SeedReceivingOpenTable extends Component {
  state = {
    rowColorRed: false
  };

  tableRowColor = () => {
    this.setState({
      rowColorRed: true
    });
  };
  render() {
    const { rowColorRed } = this.state;
    return (
      <div>
        <div className="row">
          <div className="col-6">
            <div className="row to-seed-row-color">
              <div className="col-xl-6">Ordered</div>
              <div className="col-xl-6 d-flex justify-content-md-start justify-content-xl-end">
                Total Quantity Ordered : 3000
              </div>
            </div>
            <div className=" table-responsive">
              <table className="table to-seed-table table-striped">
                <thead className="to-seed-thead">
                  <tr>
                    <th></th>
                    <th>Ball #</th>
                    <th>Vendor ID</th>
                    <th>Order Date</th>
                    <th>Sow Week</th>
                    <th>Ship Week</th>
                    <th>Species</th>
                    <th>Variety</th>
                    <th>Scanned</th>
                    <th>Ordered</th>
                  </tr>
                </thead>
                <tbody>
                  <tr
                    className={
                      rowColorRed //new stuff
                        ? "to-seed-table-row-color-red hello2"
                        : "hello"
                    }
                  >
                    <td>
                      <i className="fa fa-square-full"></i>
                    </td>
                    <td> 1234566789</td>
                    <td>4455669</td>
                    <td>06/10/2019</td>
                    <td>13/2019</td>
                    <td>25/2019</td>
                    <td>Macho Fem</td>
                    <td>Macho</td>
                    <td> 0</td>
                    <td>1000</td>
                  </tr>
                  <tr className="">
                    <td>
                      <i className="fa fa-square-full"></i>
                    </td>
                    <td> 1234566789</td>
                    <td>4455669</td>
                    <td>06/10/2019</td>
                    <td>13/2019</td>
                    <td>25/2019</td>
                    <td>Panises</td>
                    <td>Small</td>
                    <td> 0</td>
                    <td>1000</td>
                  </tr>
                  <tr className="">
                    <td>
                      <i className="fa fa-square-full"></i>
                    </td>
                    <td> 1234566789</td>
                    <td>4455669</td>
                    <td>06/10/2019</td>
                    <td>13/2019</td>
                    <td>25/2019</td>
                    <td>Black Eyed Susan</td>
                    <td>Med</td>
                    <td> 0</td>
                    <td>1000</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>

          <div className="col-6">
            <div className="row to-seed-row-color">
              <div className="col-xl-6">Received</div>
              <div className="col-xl-6 d-flex justify-content-md-start justify-content-xl-end">
                Total Quantity Received : 0
              </div>
            </div>
            <div className=" table-responsive">
              <table className="table to-seed-table">
                <thead className="to-seed-thead">
                  <tr>
                    <th>Ball #</th>
                    <th>Vendor ID</th>
                    <th>Order Date</th>
                    <th>Sow Week</th>
                    <th>Ship Week</th>
                    <th>Species</th>
                    <th>Variety</th>
                    <th>Scanned</th>
                    <th>Ordered</th>
                  </tr>
                </thead>
              </table>
            </div>
          </div>
        </div>

        <section className="button-area">
          <div className="container-fluid ">
            <div className="row">
              <div className="col-xl-2">
                <div className="to-custom-text">
                  <p>
                    <span className="scanned"></span>Scanned
                  </p>
                  <p>
                    <span className="not-scanned"></span>Not Scanned
                  </p>
                </div>
              </div>
              <div className="col-xl-2">
                <div className="to-custom-text">
                  <p>
                    <span className="more-than-expected"></span>More than
                    expected
                  </p>
                  <p>
                    <span className="less-than-expected"></span>Less than
                    expected
                  </p>
                </div>
              </div>

              <div className="col-xl-8">
                <div className="to-bottom-space">
                  <div className="BottomButtons">
                    <button
                      type="button"
                      className="btn to-button-disabled"
                      disabled
                    >
                      Edit
                    </button>
                    <button
                      data-toggle="modal"
                      data-target="#seeding-dialog20"
                      type="button"
                      className="btn to-button-default"
                    >
                      Complete
                    </button>
                    <button
                      onClick={this.tableRowColor}
                      type="button"
                      className="btn to-button-default"
                    >
                      Scan
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>

        {/* <!---Dialog - PO  20--> */}
        <section className="logout-modal">
          <div
            className="modal fade"
            id="seeding-dialog20"
            tabIndex="-1"
            role="dialog"
            aria-labelledby="exampleModalLabel"
            aria-hidden="true"
            data-backdrop="false"
          >
            <div className="modal-dialog" role="document">
              <div className="modal-content seeding-dialog23-modal-content">
                <div className="modal-body">
                  <button
                    type="button"
                    className="close"
                    data-dismiss="modal"
                    aria-label="Close"
                  >
                    <span aria-hidden="true">&times;</span>
                  </button>

                  <div className="text-white text-center seeding-dialog7-context">
                    <p>
                      Seed has been added to inventory
                      <br /> Please place in proper area.
                    </p>
                  </div>
                  <div className="text-white text-center seeding-dialog7-context">
                    <p>Do you need to Receive additional Seed?</p>
                  </div>

                  <div className="seeding-button-area">
                    <button data-dismiss="modal" className="seeding-dialog-btn">
                      No
                    </button>

                    <Link to="/seeding/seed_receiving">
                      {" "}
                      <button
                        data-backdrop="false"
                        className="seeding-dialog-btn"
                      >
                        Yes
                      </button>
                    </Link>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}
