import React, { Component } from "react";
import { Link } from "react-router-dom";
import "../App.css";

class RecentUsedModule extends Component {
  render() {
    return (
      <div>
        <div className="row">
          <div className="col-xl-12">
            <div className="recent-title text-center">
              <h3>Recently Used</h3>
            </div>
          </div>
        </div>

        <div className="row recent-used-item">
          <div className="col-xl-3 ">
            <div className="recent-item-box text-center">
              <img
                src={require("../assets/icons/icon_purchasing_main.svg")}
                alt=""
              />
              <span>
                <Link to="/purchasing/dashboard">Purchasing</Link>
              </span>
            </div>
          </div>
          <div className="col-xl-3 ">
            <div className="recent-item-box text-center">
              <img src={require("../assets/icons/icon_patching.svg")} alt="" />
              <span>
                <Link to="/patching/dashboard">Patching</Link>
              </span>
            </div>
          </div>
          <div className="col-xl-3">
            <div className="recent-item-box text-center">
              <img src={require("../assets/icons/icon_cutting.svg")} alt="" />
              <span>
                <Link to="/cuttings/dashboard">Cuttings</Link>
              </span>
            </div>
          </div>
          <div className="col-xl-3">
            <div className="recent-item-box text-center">
              <img
                src={require("../assets/icons/icon_transplanting.png")}
                alt=""
              />
              <span>
                <a href="#/">Transplanting</a>
              </span>
            </div>
          </div>
        </div>

        <div className="row recent-used-item">
          <div className="col-xl-3 ">
            <div className="recent-item-box text-center">
              <img
                src={require("../assets/icons/icon_forecasting.png")}
                alt=""
              />
              <span>
                <a href="#/">Forecasting</a>
              </span>
            </div>
          </div>
          <div className="col-xl-3 ">
            <div className="recent-item-box text-center">
              <img src={require("../assets/icons/icon_shipping.svg")} alt="" />
              <span>
                <a href="#/">Shipping</a>
              </span>
            </div>
          </div>
          <div className="col-xl-3 ">
            <div className="recent-item-box text-center">
              <img src={require("../assets/icons/icon_quality.png")} alt="" />
              <span>
                <a href="#/">Quality</a>
              </span>
            </div>
          </div>
          <div className="col-xl-3">
            <div className="recent-item-box text-center">
              <img src={require("../assets/icons/icon_growing.svg")} alt="" />
              <span>
                <Link to="/growing/dashboard">Growing</Link>
              </span>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default RecentUsedModule;
