import React, { Component } from "react";
import Header from "../../components/Header";
import ReceiveCuttingsForm from "./Components/ReceiveCuttingsForm";
import ReceiveCuttingsTables from "./Components/ReceiveCuttingsTables";
import CopyRightsText from "../../components/CopyRightsText";

class ReceiveCuttings extends Component {
  componentDidMount() {
    document.title = "Receive Cuttings";
  }
  render() {
    return (
      <div>
        <Header pageTitle="Receive Cuttings" />
        <ReceiveCuttingsForm />
        <ReceiveCuttingsTables />
        <CopyRightsText />
      </div>
    );
  }
}

export default ReceiveCuttings;
