import React, { Component } from "react";
import Header from "../../components/Header";
import SeedReceivingHeader from "./Components/SeedReceivingHeader";
import SeedReceivingMainTable from "./Components/SeedReceivingMainTable";

class SeedReceiving extends Component {
  componentDidMount() {
    document.title = "Seed Receiving";
  }
  render() {
    return (
      <div>
        <Header pageTitle="Seed Receiving" />
        <SeedReceivingHeader />

        <SeedReceivingMainTable />
      </div>
    );
  }
}

export default SeedReceiving;
