import React, { Component } from "react";
import { Link } from "react-router-dom";
export default class GrowingSubModules extends Component {
  render() {
    return (
      <div>
        <div className="row">
          <div className="col-xl-12">
            <div className="sub-module-title text-center">
              <h3>Recently Viewed</h3>
            </div>
          </div>
        </div>
        <div className="row recent-used-item">
          <div className="col-sm-3 ">
            <div className="subModule-item-box">
              <img
                src={require("../../../assets/icons/growing-plant.png")}
                alt=""
              />
              <span>
                <Link to="/growing/daily_task">Daily Tasks</Link>
              </span>
            </div>
          </div>
          <div className="col-sm-3 ">
            <div className="subModule-item-box">
              <img
                src={require("../../../assets/icons/sprinkler.png")}
                alt=""
              />
              <span>
                <Link to="/growing/spray">Spray</Link>
              </span>
            </div>
          </div>
          <div className="col-sm-3 ">
            <div className="subModule-item-box">
              <img
                src={require("../../../assets/icons/shopping-list.png")}
                alt=""
              />
              <span>
                {" "}
                <Link to="/growing/spray_records">Spray Records</Link>
              </span>
            </div>
          </div>
          <div className="col-sm-3">
            <div className="subModule-item-box">
              <img src={require("../../../assets/icons/study.png")} alt="" />
              <span>
                {" "}
                <Link to="/growing/training">Training</Link>
              </span>
            </div>
          </div>
        </div>
        <div className="row recent-used-item">
          <div className="col-sm-3 ">
            <div className="subModule-item-box">
              <img src={require("../../../assets/icons/writing.png")} alt="" />
              <span>
                {" "}
                <Link to="/growing/notes_assignments">Notes / Assignments</Link>
              </span>
            </div>
          </div>
          <div className="col-sm-3 ">
            <div className="subModule-item-box">
              <img src={require("../../../assets/icons/bottle.png")} alt="" />
              <span>
                <Link to="/growing/chemical_inventory">Chemical Inventory</Link>
              </span>
            </div>
          </div>
          <div className="col-sm-3 ">
            <div className="subModule-item-box">
              <img src={require("../../../assets/icons/audit.png")} alt="" />
              <span>
                <Link to="#/">Report</Link>
              </span>
            </div>
          </div>
          <div className="col-sm-3 ">
            <div className="subModule-item-box">
              <img src={require("../../../assets/icons/audit.png")} alt="" />
              <span>
                <Link to="#/">Report</Link>
              </span>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
