import React, { Component } from "react";
import { Tabs, Tab } from "react-bootstrap";
import NewPurchaseHeader from "./NewPurchaseHeader";
import NewPurchaseReviewNotifications from "./NewPurchaseReviewNotifications";

class NewPurchaseOrderContent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      key: 1,
      showByItem: true,
      showByOrder: false,
      showByItemTable: false,
      showReviewByItem: true,
      showReviewByOrder: false,
      tabDisabled: true
    };
    this.handleSelect = this.handleSelect.bind(this);
    this.OnShowClick = this.OnShowClick.bind(this);
    this.OnShowClick2 = this.OnShowClick2.bind(this);
    this.OnShowClick3 = this.OnShowClick3.bind(this);
    this.OnShowClick4 = this.OnShowClick4.bind(this);
  }
  handleSelect(key) {
    this.setState({ key });
  }
  TabDisabledChange = () => {
    this.setState({ tabDisabled: false });
  };
  OnShowClick = () => {
    this.setState({
      showByItem: false,
      showByOrder: true
    });
  };
  OnShowClick2 = () => {
    this.setState({
      showByItem: true,
      showByOrder: false,
      showByItemTable: false
    });
  };
  OnShowClick3 = () => {
    this.setState({
      showReviewByItem: false,
      showReviewByOrder: true
    });
  };
  OnShowClick4 = () => {
    this.setState({
      showReviewByItem: true,
      showReviewByOrder: false
    });
  };

  OnShowTable = () => {
    this.setState({ showByItemTable: true });
  };

  render() {
    const { showByItem } = this.state;
    const { showByOrder } = this.state;
    const { showByItemTable } = this.state;
    const { showReviewByItem } = this.state;
    const { showReviewByOrder } = this.state;
    const { tabDisabled } = this.state;

    return (
      <div>
        <Tabs
          className="customtabs"
          activeKey={this.state.key}
          onSelect={this.handleSelect}
          id="controlled-tab-example"
        >
          <Tab eventKey={1} title="1. Purchase Header">
            <NewPurchaseHeader />
            <section className="button-area">
              <div className="container text-center">
                <div className="BottomButtons">
                  <button
                    onClick={() => {
                      this.handleSelect("2");
                      this.OnShowClick2();
                    }}
                    type="button"
                    className="btn to-button-default"
                  >
                    By Item
                  </button>
                  <button
                    onClick={() => {
                      this.handleSelect("2");
                      this.OnShowClick();
                    }}
                    type="button"
                    className="btn to-button-default"
                  >
                    By Order
                  </button>
                </div>
              </div>
            </section>
          </Tab>
          <Tab eventKey={2} title="2. Purchase Details" disabled={tabDisabled}>
            {showByItem ? (
              <div className="container-fluid ">
                <form className="es_purchase-input">
                  <div className="form-row">
                    <div className="form-group col-md-3">
                      <label className="to-title-f-p-size" htmlFor="inputTerms">
                        Item Type
                      </label>
                      <select
                        id="inputTerms"
                        className="form-control to-input-f-b-c to-classic"
                      >
                        <option defaultValue>Plastics</option>
                        <option>...</option>
                      </select>
                    </div>
                    <div className="form-group col-md-3">
                      <label
                        className="to-title-f-p-size"
                        htmlFor="inputPurchaseOrder"
                      >
                        Item Cost
                      </label>
                      <input
                        type="text"
                        className="form-control to-input-f-b-c"
                        id="inputPurchaseOrder"
                        placeholder="$125.00"
                      />
                    </div>
                    <div className="form-group col-md-3">
                      <label
                        className="to-title-f-p-size"
                        htmlFor="inputVendor"
                      >
                        Item Description
                      </label>
                      <input
                        type="text"
                        className="form-control to-input-f-b-c"
                        id="inputVendor"
                        placeholder="Green Plastic thing"
                      />
                    </div>
                    <div className="form-group col-md-3">
                      <label
                        className="to-title-f-p-size"
                        htmlFor="inputCreator"
                      >
                        # of Units/Loads
                      </label>
                      <input
                        type="text"
                        className="form-control to-input-f-b-c"
                        id="inputCreator"
                        placeholder="5"
                      />
                    </div>
                  </div>

                  <div className="form-row">
                    <div className="form-group col-md-3">
                      <label className="to-title-f-p-size" htmlFor="inputTerms">
                        Shipping Type
                      </label>
                      <select
                        id="inputTerms"
                        className="form-control to-input-f-b-c to-classic"
                      >
                        <option defaultValue>Shipping Type</option>
                        <option>...</option>
                      </select>
                    </div>

                    <div className="form-group col-md-3">
                      <label
                        className="to-title-f-p-size"
                        htmlFor="inputDiscounts"
                      >
                        Surcharge
                      </label>
                      <input
                        type="text"
                        className="form-control to-input-f-b-c"
                        id="inputDiscounts"
                        placeholder="$10.00"
                      />
                    </div>
                    <div className="form-group col-md-3">
                      <label
                        className="to-title-f-p-size"
                        htmlFor="inputDescriptionOrder"
                      >
                        Ship Date
                      </label>
                      <input
                        type="date"
                        className="form-control to-input-f-b-c"
                        id="inputDescriptionOrder"
                        placeholder="Order description"
                      />
                    </div>
                    <div className="form-group col-md-3">
                      <div className="form-check">
                        <label
                          className="to-title-f-p-size"
                          htmlFor="inputDescriptionOrder"
                        >
                          FOB
                        </label>{" "}
                        <br />
                        <input
                          className="form-check-input position-static es_purchase-checkbox to-input-f-b-c"
                          type="checkbox"
                          id="blankCheckbox"
                          value="option1"
                          aria-label="..."
                        />
                      </div>
                    </div>
                  </div>
                </form>

                <section className="button-area">
                  <div className="container">
                    <div className="row">
                      <div className="col-12">
                        <div className="text-center">
                          <button
                            onClick={this.OnShowTable}
                            type="button"
                            className="btn to-button-default forced-center"
                          >
                            + Add Item
                          </button>
                        </div>
                      </div>
                    </div>
                  </div>
                </section>

                {showByItemTable ? (
                  <div className="container-fluid">
                    <table className="table table-bordered">
                      <col width="50" />

                      <thead className="thead-dark vbm-thead">
                        <tr>
                          <th>
                            <i className="fa fa-square"></i>
                          </th>
                          <th>Item Type</th>
                          <th>Item Cost</th>
                          <th>Description</th>
                          <th># of Units</th>
                          <th>Shipping Type</th>
                          <th>Surcharge</th>
                          <th>Ship Date</th>
                          <th>FOB</th>
                        </tr>
                      </thead>
                      <tbody className="es_tableBody1">
                        <tr className="ReceiverOrderTableRow">
                          <td>
                            <div className="form-check-inline">
                              <label className="form-check-label">
                                <input
                                  type="checkbox"
                                  className="form-check-input checkbox-1x"
                                  value=""
                                />
                              </label>
                            </div>
                          </td>
                          <td>Dirt</td>
                          <td>$1000.00</td>
                          <td>Great dirt</td>
                          <td>10</td>
                          <td>Shipping Type</td>
                          <td></td>
                          <td> 05/25/2019</td>
                          <td></td>
                        </tr>
                      </tbody>
                    </table>
                    <div className="row">
                      <div className="col-12">
                        <div className="RunningPOCheck">
                          <div className="text-right">
                            <ul className="list-inline RunningPOCheck">
                              <li className="list-inline-item">
                                <label className="checkbox-inline">
                                  <input
                                    style={{ marginRight: "1rem" }}
                                    type="checkbox"
                                    className="checkbox-1x"
                                    defaultChecked
                                    value=""
                                  />
                                  Running PO
                                </label>
                              </li>
                            </ul>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className=" BottomButtons text-center">
                      <button type="button" className="btn to-button-default">
                        Add
                      </button>
                      <button
                        disabled
                        type="button"
                        className="btn to-button-disabled"
                      >
                        Edit
                      </button>

                      <button disabled className="btn to-button-disabled">
                        Delete
                      </button>
                      <button
                        onClick={() => {
                          this.handleSelect("3");
                          this.OnShowClick4();
                          this.TabDisabledChange();
                        }}
                        className="btn btn to-button-default"
                      >
                        Review Order
                      </button>
                    </div>
                  </div>
                ) : null}
              </div>
            ) : null}
            {showByOrder ? (
              <div className="container-fluid ">
                <form className="es_purchase-input">
                  <div className="form-row">
                    <div className="form-group col-md-3">
                      <label className="to-title-f-p-size" htmlFor="inputUnit">
                        # Unit/Loads
                      </label>
                      <input
                        type="text"
                        className="form-control to-input-f-b-c"
                        id="inputUnit"
                        placeholder="25"
                      />
                    </div>
                    <div className="form-group col-md-3">
                      <label className="to-title-f-p-size" htmlFor="inputFOB">
                        FOB
                      </label>
                      <input
                        type="text"
                        className="form-control to-input-f-b-c"
                        id="inputFOB"
                      />
                    </div>
                    <div className="form-group col-md-3">
                      <label
                        className="to-title-f-p-size"
                        htmlFor="inputSurcharge"
                      >
                        Surcharge
                      </label>
                      <input
                        type="text"
                        className="form-control to-input-f-b-c"
                        id="inputSurcharge"
                        placeholder="$50.00"
                      />
                    </div>
                    <div className="form-group col-md-3">
                      <label
                        className="to-title-f-p-size"
                        htmlFor="inputCreator"
                      >
                        Ship Date
                      </label>
                      <input
                        type="date"
                        className="form-control to-input-f-b-c"
                        id="inputCreator"
                        placeholder="05/10/2019"
                      />
                    </div>
                  </div>
                </form>

                <section className="button-area">
                  <div className="container text-center">
                    <div className="BottomButtons">
                      <button
                        onClick={() => this.handleSelect("1")}
                        type="button"
                        className="btn to-button-default"
                      >
                        Go Back
                      </button>
                      <button
                        onClick={() => {
                          this.handleSelect("3");
                          this.OnShowClick3();
                          this.TabDisabledChange();
                        }}
                        type="button"
                        className="btn to-button-default"
                      >
                        Review Order
                      </button>
                    </div>
                  </div>
                </section>
              </div>
            ) : null}
          </Tab>
          <Tab eventKey={3} title="3. Purchase Review" disabled={tabDisabled}>
            {showReviewByOrder ? (
              <div className="container-fluid ">
                <form className="es_purchase-input">
                  <div className="form-row">
                    <div className="form-group col-md-3">
                      <label
                        className="to-title-f-p-size"
                        htmlFor="inputOrderDate"
                      >
                        Order Date
                      </label>
                      <input
                        type="date"
                        className="form-control to-input-f-b-c"
                        id="inputEmail4"
                        placeholder="04/10/2019"
                      />
                    </div>
                    <div className="form-group col-md-3">
                      <label
                        className="to-title-f-p-size"
                        htmlFor="inputPurchaseOrder"
                      >
                        Purchase Order #
                      </label>
                      <input
                        type="text"
                        className="form-control to-input-f-b-c"
                        id="inputPurchaseOrder"
                        placeholder="246813579"
                      />
                    </div>
                    <div className="form-group col-md-3">
                      <label
                        className="to-title-f-p-size"
                        htmlFor="inputVendor"
                      >
                        Vendor
                      </label>
                      <input
                        type="text"
                        className="form-control to-input-f-b-c"
                        id="inputVendor"
                        placeholder="ACME COMPANY"
                      />
                    </div>
                    <div className="form-group col-md-3">
                      <label
                        className="to-title-f-p-size"
                        htmlFor="inputCreator"
                      >
                        Creator
                      </label>
                      <input
                        type="text"
                        className="form-control to-input-f-b-c"
                        id="inputCreator"
                        placeholder="Greg Brewster"
                      />
                    </div>
                  </div>

                  <div className="form-row">
                    <div className="form-group col-md-3">
                      <label
                        className="to-title-f-p-size"
                        htmlFor="inputSalesOrder"
                      >
                        Sales Order #
                      </label>
                      <input
                        type="text"
                        className="form-control to-input-f-b-c"
                        id="inputSalesOrder"
                        placeholder="0987654321"
                      />
                    </div>
                    <div className="form-group col-md-3">
                      <div className="row">
                        <div className="col">
                          <label
                            className="to-title-f-p-size"
                            htmlFor="inputTerms"
                          >
                            Terms
                          </label>
                          <select className="form-control to-input-f-b-c to-classic">
                            <option defaultValue>Net 30</option>
                            <option>...</option>
                          </select>
                        </div>

                        <div className="col">
                          <label
                            className="to-title-f-p-size"
                            htmlFor="inputDept"
                          >
                            Dept
                          </label>
                          <input
                            type="text"
                            className="form-control to-input-f-b-c"
                            id="inputDept"
                          />
                        </div>
                      </div>
                    </div>

                    <div className="form-group col-md-3">
                      <label
                        className="to-title-f-p-size"
                        htmlFor="inputDiscounts"
                      >
                        Discounts
                      </label>
                      <input
                        type="text"
                        className="form-control to-input-f-b-c"
                        id="inputDiscounts"
                        placeholder="Discounts"
                      />
                    </div>
                    <div className="form-group col-md-3">
                      <label
                        className="to-title-f-p-size"
                        htmlFor="inputDescriptionOrder"
                      >
                        Description of Order
                      </label>
                      <input
                        type="text"
                        className="form-control to-input-f-b-c"
                        id="inputDescriptionOrder"
                        placeholder="Order description"
                      />
                    </div>
                  </div>

                  <div className="form-row">
                    <div className="form-group col-md-3">
                      <label className="to-title-f-p-size" htmlFor="inputUnit">
                        # Unit/Loads
                      </label>
                      <input
                        type="text"
                        className="form-control to-input-f-b-c"
                        id="inputUnit"
                        placeholder="25"
                      />
                    </div>
                    <div className="form-group col-md-3">
                      <label className="to-title-f-p-size" htmlFor="inputFOB">
                        FOB
                      </label>
                      <input
                        type="text"
                        className="form-control to-input-f-b-c"
                        id="inputFOB"
                      />
                    </div>
                    <div className="form-group col-md-3">
                      <label
                        className="to-title-f-p-size"
                        htmlFor="inputSurcharge"
                      >
                        Surcharge
                      </label>
                      <input
                        type="text"
                        className="form-control to-input-f-b-c"
                        id="inputSurcharge"
                        placeholder="$50.00"
                      />
                    </div>
                    <div className="form-group col-md-3">
                      <label
                        className="to-title-f-p-size"
                        htmlFor="inputCreator"
                      >
                        Ship Date
                      </label>
                      <input
                        type="text"
                        className="form-control to-input-f-b-c"
                        id="inputCreator"
                        placeholder="05/10/2019"
                      />
                    </div>
                  </div>
                </form>
                <section className="button-area">
                  <div className="container text-center">
                    <div className="BottomButtons">
                      <button
                        href="#/"
                        data-toggle="modal"
                        data-target="#dialogPo"
                        type="button"
                        className="btn to-button-default"
                      >
                        Submit Order
                      </button>
                      <button
                        href="#/"
                        data-toggle="modal"
                        data-target="#dialogPo5"
                        type="button"
                        className="btn to-button-default"
                      >
                        Email
                      </button>
                    </div>
                  </div>
                </section>
              </div>
            ) : null}

            {showReviewByItem ? (
              <div className="container-fluid ">
                <form className="es_purchase-input">
                  <div className="form-row">
                    <div className="form-group col-md-3">
                      <label
                        className="to-title-f-p-size"
                        htmlFor="inputOrderDate"
                      >
                        Order Date
                      </label>
                      <input
                        type="date"
                        className="form-control to-input-f-b-c"
                        id="inputEmail4"
                        placeholder="04/10/2019"
                      />
                    </div>
                    <div className="form-group col-md-3">
                      <label
                        className="to-title-f-p-size"
                        htmlFor="inputPurchaseOrder"
                      >
                        Purchase Order #
                      </label>
                      <input
                        type="text"
                        className="form-control to-input-f-b-c"
                        id="inputPurchaseOrder"
                        placeholder="246813579"
                      />
                    </div>
                    <div className="form-group col-md-3">
                      <label
                        className="to-title-f-p-size"
                        htmlFor="inputVendor"
                      >
                        Vendor
                      </label>
                      <input
                        type="text"
                        className="form-control to-input-f-b-c"
                        id="inputVendor"
                        placeholder="ACME COMPANY"
                      />
                    </div>
                    <div className="form-group col-md-3">
                      <label
                        className="to-title-f-p-size"
                        htmlFor="inputCreator"
                      >
                        Creator
                      </label>
                      <input
                        type="text"
                        className="form-control to-input-f-b-c"
                        id="inputCreator"
                        placeholder="Greg Brewster"
                      />
                    </div>
                  </div>

                  <div className="form-row">
                    <div className="form-group col-md-3">
                      <label
                        className="to-title-f-p-size"
                        htmlFor="inputSalesOrder"
                      >
                        Sales Order #
                      </label>
                      <input
                        type="text"
                        className="form-control to-input-f-b-c"
                        id="inputSalesOrder"
                        placeholder="0987654321"
                      />
                    </div>
                    <div className="form-group col-md-3">
                      <div className="row">
                        <div className="col">
                          <label
                            className="to-title-f-p-size"
                            htmlFor="inputTerms"
                          >
                            Terms
                          </label>
                          <select className="form-control to-input-f-b-c to-classic">
                            <option defaultValue>Net 30</option>
                            <option>...</option>
                          </select>
                        </div>

                        <div className="col">
                          <label
                            className="to-title-f-p-size"
                            htmlFor="inputDept"
                          >
                            Dept
                          </label>
                          <input
                            type="text"
                            className="form-control to-input-f-b-c"
                            id="inputDept"
                          />
                        </div>
                      </div>
                    </div>

                    <div className="form-group col-md-3">
                      <label
                        className="to-title-f-p-size"
                        htmlFor="inputDiscounts"
                      >
                        Discounts
                      </label>
                      <input
                        type="text"
                        className="form-control to-input-f-b-c"
                        id="inputDiscounts"
                        placeholder="Discounts"
                      />
                    </div>
                    <div className="form-group col-md-3">
                      <label
                        className="to-title-f-p-size"
                        htmlFor="inputDescriptionOrder"
                      >
                        Description of Order
                      </label>
                      <input
                        type="text"
                        className="form-control to-input-f-b-c"
                        id="inputDescriptionOrder"
                        placeholder="Order description"
                      />
                    </div>
                  </div>
                </form>
                <table className="table table-bordered">
                  <col width="50" />
                  <thead className="thead-dark vbm-thead">
                    <tr>
                      <th>
                        <i className="fa fa-square"></i>
                      </th>
                      <th>Item Type</th>
                      <th>Item Cost</th>
                      <th>Description</th>
                      <th># of Units</th>
                      <th>Shipping Type</th>
                      <th>Surcharge</th>
                      <th>Ship Date</th>
                      <th>FOB</th>
                      <th>Status</th>
                    </tr>
                  </thead>
                  <tbody className="es_tableBody1">
                    <tr className="ReceiverOrderTableRow">
                      <td>
                        {" "}
                        <div className="form-check-inline">
                          <label className="form-check-label">
                            <input
                              type="checkbox"
                              className="form-check-input checkbox-1x"
                              value=""
                            />
                          </label>
                        </div>
                      </td>
                      <td>Dirt</td>
                      <td>$1000.00</td>
                      <td>Great dirt</td>
                      <td>11</td>
                      <td>Shipping Type</td>
                      <td></td>
                      <td> 05/25/2019</td>
                      <td></td>
                    </tr>

                    <tr className="ReceiverOrderTableRowTotals">
                      <td></td>
                      <td>Totals</td>
                      <td>$1000.00</td>
                      <td></td>
                      <td>1</td>
                      <td></td>
                      <td></td>
                      <td></td>
                      <td></td>
                      <td></td>
                    </tr>
                  </tbody>
                </table>
                <div className="row">
                  <div className="col-12">
                    <div className="RunningPOCheck">
                      <div className="text-right">
                        <ul className="list-inline RunningPOCheck">
                          <li className="list-inline-item">
                            <label className="checkbox-inline">
                              <input
                                style={{ marginRight: "1rem" }}
                                type="checkbox"
                                className="checkbox-1x"
                                defaultChecked
                                value=""
                              />
                              Running PO
                            </label>
                          </li>
                        </ul>
                      </div>
                    </div>
                  </div>
                </div>
                <div className=" BottomButtons text-center">
                  <button type="button" className="btn to-button-default">
                    Edit Order
                  </button>

                  <button
                    data-toggle="modal"
                    data-target="#dialogPo"
                    type="button"
                    className="btn to-button-default"
                  >
                    Submit Order
                  </button>
                  <button
                    data-toggle="modal"
                    data-target="#dialogPo5"
                    type="button"
                    className="btn to-button-default"
                  >
                    Email
                  </button>
                </div>
              </div>
            ) : null}

            <NewPurchaseReviewNotifications />
          </Tab>
        </Tabs>
      </div>
    );
  }
}

export default NewPurchaseOrderContent;
