import React, { Component } from "react";
import Header from "../../components/Header";
import SearchFilterArea from "../../components/SearchFilterArea";
import ProductionTable from "./Components/ProductionTable";
import "../../App.css";
import CopyRightsText from "../../components/CopyRightsText";
import CuttingsProductionNotifications from "./Components/CuttingsProductionNotifications";

class CuttingsProduction extends Component {
  componentDidMount() {
    document.title = "Cuttings Production";
  }
  render() {
    return (
      <div>
        <Header pageTitle="Cuttings Production" />
        <SearchFilterArea placeholderText="Search Filter Area" />
        <ProductionTable />
        <CuttingsProductionNotifications />
        <CopyRightsText />
      </div>
    );
  }
}

export default CuttingsProduction;
