import React, { Component } from "react";
import "../App.css";
import UserDetailsPermissionSection from "../components/UserDetailsPermissionSection";
import Header from "../components/Header";

class UserPermissions extends Component {
  componentDidMount() {
    document.title = "User Permissions";
  }
  render() {
    return (
      <div>
        <Header pageTitle="User Permissions" />
        <div className="row to-custom-padding">
          <div className="col-md-6">
            <UserDetailsPermissionSection />
          </div>
        </div>

        <section className="to-permission-button-area-2">
          <div className="row">
            <div className="col-lg-6 offset-lg-6 col-sm-12 ">
              <div className="d-flex justify-content-lg-end justify-content-md-center justify-content-sm-center ">
                <a href="#/" className="button-default to-button-default ">
                  Save
                </a>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

export default UserPermissions;
