import React, { Component } from "react";

class SearchByGridSearchFilterArea extends Component {
  render() {
    return (
      <div>
        <div className="to-filter-bg">
          <div className="row">
            <div className="col-md-4 col-sm-12">
              <div className="to-filter-text">
                Add filters from the search box
              </div>
            </div>
            <div className="col-md-4 offset-md-4  text-right">
              <div className="input-group to-input-tb">
                <input
                  className="form-control py-2"
                  placeholder="Search filter example"
                />
                <span className="input-group-append">
                  <button
                    className="btn btn-outline-secondary to-filter-btn"
                    type="button"
                  >
                    <i className="fa fa-search to-icon-color"></i>
                  </button>
                </span>
              </div>
              <div className="row">
                <div className="col-6 offset-6 mb-2">
                  <select
                    id="inputTerms"
                    className="form-control to-input-f-b-c to-classic-seed "
                  >
                    <option defaultValue>DR1</option>
                    <option>...</option>
                  </select>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default SearchByGridSearchFilterArea;
