import React, { Component } from "react";
import "../../../App.css";
export default class BobsInternalFilter extends Component {
  render() {
    return (
      <div>
        <div className="to-filter-bg">
          <div className="row">
            <div className="col-md-2 col-sm-12 ">
              <div className="form-group dropdownCenter">
                <label className="es_inputDropdownLebel" for="inputState">
                  Season
                </label>

                <select
                  id="inputTerms"
                  className="form-control to-input-f-b-c to-classic-seed"
                >
                  <option defaultValue>Spring/2019</option>
                  <option>...</option>
                </select>
              </div>
            </div>
            <div className="col-md-3 offset-md-7  text-right">
              <div
                style={{ marginTop: "8%" }}
                className="input-group to-input-tb"
              >
                <input className="form-control py-2" placeholder="Add filter" />
                <span className="input-group-append">
                  <button
                    className="btn btn-outline-secondary to-filter-btn"
                    type="button"
                  >
                    <i className="fa fa-search to-icon-color"></i>
                  </button>
                </span>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
