import React, { Component } from "react";
import SeedReceivingHeader from "./Components/SeedReceivingHeader";
import Header from "../../components/Header";
import SeedReceivingOpenTable from "./Components/SeedReceivingOpenTable";

class SeedReceivingOpenPO extends Component {
  componentDidMount() {
    document.title = "Seed Receiving";
  }
  render() {
    return (
      <div>
        <Header pageTitle="Seed Receiving" />
        <SeedReceivingHeader />
        <SeedReceivingOpenTable />
      </div>
    );
  }
}

export default SeedReceivingOpenPO;
