import React, { Component } from "react";
import Header from "../../components/Header";
import SprayForm from "./Components/SprayForm";
import CopyRightsText from "../../components/CopyRightsText";
class Spray extends Component {
  componentDidMount() {
    document.title = "Spray";
  }
  render() {
    return (
      <div>
        <Header pageTitle="Spray" />
        <SprayForm />
        <div id="pur-dash-plusNotificationBar">
          <i className="fa fa-plus"></i>
        </div>
        <CopyRightsText />
      </div>
    );
  }
}

export default Spray;
