import React, { Component } from "react";
import Header from "../../components/Header";
import InventoyCuttingsTable from "./Components/InventoyCuttingsTable";
import CopyRightsText from "../../components/CopyRightsText";

class InventoryCuttings extends Component {
  componentDidMount() {
    document.title = "Inventoy Cuttings";
  }
  render() {
    return (
      <div>
        <Header pageTitle="Inventoy Cuttings" />
        <InventoyCuttingsTable />
        <CopyRightsText />
      </div>
    );
  }
}

export default InventoryCuttings;
