import React, { Component } from "react";
import Header from "../../components/Header";
import InventoryAssignmentHeader from "./Components/InventoryAssignmentHeader";
import InventoryAssignmentTable from "./Components/InventoryAssignmentTable";

import CopyRightsText from "../../components/CopyRightsText";

class InventoryAssginment extends Component {
  componentDidMount() {
    document.title = "Seed Inventory Assignment";
  }
  render() {
    return (
      <div>
        <Header pageTitle="Seed Inventory Assignment" />
        <InventoryAssignmentHeader />
        <InventoryAssignmentTable />

        <CopyRightsText />
      </div>
    );
  }
}

export default InventoryAssginment;
