import React, { Component } from "react";
import InputForms from "../components/InputForms";
import "../App.css";
import UserDetailsPermissionSection from "../components/UserDetailsPermissionSection";
import Header from "../components/Header";

class UserDetails extends Component {
  componentDidMount() {
    document.title = "User Details";
  }
  render() {
    return (
      <div>
        <Header pageTitle="User Details" />
        <InputForms />
        <div className="row to-custom-padding">
          <div className="col-md-6">
            <p className="to-role-permission-title-to">Roles</p>
            <div className="form-group ">
              <select className="custom-select" multiple>
                <option defaultValue>Role One</option>
                <option>Role Two</option>
                <option defaultValue>Role Three</option>
                <option>Role Four</option>
                <option>Role Five</option>
              </select>
            </div>
          </div>
          <div className="col-md-6">
            <UserDetailsPermissionSection />
          </div>
        </div>
        <section className="to-permission-button-area">
          <div className="row">
            <div className="col-lg-6 offset-lg-6">
              <div className="d-flex justify-content-lg-end justify-content-md-center ">
                <a
                  href="#/"
                  className="button-default to-button-default button-dark"
                >
                  Delete
                </a>

                <a href="#/" className="button-default to-button-default ">
                  Save
                </a>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

export default UserDetails;
