import React, { Component } from "react";

class SeedDemandMainListHeader extends Component {
  render() {
    return (
      <div>
        <div className="to-filter-bg-2">
          <div className="row">
            <div className="col-lg-4  col-sm-12">
              <div className="to-filter-text">
                Add filters from the search box
              </div>
            </div>
            <div className="col-lg-4 offset-lg-4  text-right">
              <div className="input-group to-input-tb">
                <input
                  className="form-control py-2"
                  placeholder="Search filter example"
                />
                <span className="input-group-append">
                  <button
                    className="btn btn-outline-secondary to-filter-btn"
                    type="button"
                  >
                    <i className="fa fa-search to-icon-color"></i>
                  </button>
                </span>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default SeedDemandMainListHeader;
