import React, { Component } from "react";
import Header from "../../components/Header";

import DailyTaskNotification from "./Components/DailyTaskNotification";
import DailyTaskHouseDR1 from "./Components/DailyTaskHouseDR1";

class DailyTask extends Component {
  componentDidMount() {
    document.title = "Daily Task";
  }
  render() {
    return (
      <div>
        <Header pageTitle="Daily Task" />
        <DailyTaskHouseDR1 />
        <DailyTaskNotification />
      </div>
    );
  }
}

export default DailyTask;
