import React, { Component } from "react";
import Header from "../../components/Header";
import SowDetailsHeader from "./Components/SowDetailsHeader";
import SowDetailsTable from "./Components/SowDetailsTable";
import CopyRightsText from "../../components/CopyRightsText";

class SowDetails extends Component {
  componentDidMount() {
    document.title = "Sow Details";
  }
  render() {
    return (
      <div>
        <Header pageTitle="Sow Details" />
        <SowDetailsHeader />
        <SowDetailsTable />
        <CopyRightsText />
      </div>
    );
  }
}

export default SowDetails;
