import React, { Component } from "react";
import Header from "../../components/Header";
import SearchFilterArea from "../../components/SearchFilterArea";
import ExtrasTable from "./Components/ExtrasTable";
import ExtrasNotification from "./Components/ExtrasNotification";
import CopyRightsText from "../../components/CopyRightsText";

class Extras extends Component {
  componentDidMount() {
    document.title = "Extras";
  }
  render() {
    return (
      <div>
        <Header pageTitle="Extras" />
        <SearchFilterArea placeholderText="Search Filter Area" />
        <ExtrasTable />
        <ExtrasNotification />
        <CopyRightsText />
      </div>
    );
  }
}

export default Extras;
