import React, { Component } from "react";
import { Link } from "react-router-dom";
import NotificationBoxIconsSlider from "../../../components/NotificationBoxIconsSlider";

class SearchOrderNotificatoins extends Component {
  state = {
    showNotification: false
  };
  OnShowClick = () => {
    this.setState({ showNotification: !this.state.showNotification });
  };
  render() {
    const { showNotification } = this.state;
    return (
      <div>
        <div id="plusNotificationBar">
          <i onClick={this.OnShowClick} className="fa fa-plus"></i>
        </div>
        {showNotification ? (
          <div className="to-Patching-list-data">
            <div className="container">
              <div className="row">
                <div className="col-6 d-flex justify-content-sm-start justify-content-xs-start">
                  <div className="chooseModuleHeader-to">
                    <p>Notification</p>
                  </div>
                </div>
                <div className="col-6 d-flex justify-content-sm-end justify-content-xs-end">
                  <div className="chooseModuleIcon-to ">
                    <ul>
                      <li>
                        <Link to="/dashboard">
                          <i className="fa fa-home"></i>
                        </Link>
                      </li>
                      <li className="iconModuleClose">
                        <i
                          onClick={this.OnShowClick}
                          className="fa fa-times"
                        ></i>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>

              <NotificationBoxIconsSlider />

              <div className="row mt-3">
                <div className="col-sm-12 col-md-6">
                  <div className="to-Purchasing-action-menu-box">
                    <a
                      href="#/"
                      data-toggle="modal"
                      data-target="#diallogPo7"
                      data-dismiss="modal"
                    >
                      Create <br /> View
                    </a>
                  </div>
                </div>
                <div className="col-sm-12 col-md-6">
                  <div className="to-Purchasing-action-menu-box">
                    <a href="#/">
                      Saved <br /> View
                    </a>
                  </div>
                </div>
              </div>

              <div className="row">
                <div className="col-sm-12">
                  <div className="notification-box-padding-2x"></div>
                </div>
              </div>
            </div>
          </div>
        ) : null}

        {/* <!-- Dialog - PO  7 modal --> */}
        <section className="logout-modal">
          <div
            className="modal fade"
            id="diallogPo7"
            tabIndex="-1"
            role="dialog"
            aria-labelledby="exampleModalLabel"
            aria-hidden="true"
          >
            <div className="modal-dialog" role="document">
              <div className="modal-content">
                <div className="modal-body">
                  <button
                    type="button"
                    className="close"
                    data-dismiss="modal"
                    aria-label="Close"
                  >
                    <span aria-hidden="true">&times;</span>
                  </button>

                  <div className="modal-text-content diallogPo7Content">
                    <p>Save View As</p>
                  </div>
                  <div className="diallogPo7-form">
                    <form action="">
                      <div className="form-group">
                        <input
                          type="text"
                          className="form-control"
                          placeholder="Gerg's Custom Purchase View"
                        />
                      </div>
                      <div className="form-group">
                        <input
                          type="text"
                          className="form-control"
                          placeholder="This is descriptions of the view"
                        />
                      </div>
                      <div className="form-check">
                        <input
                          type="checkbox"
                          className="form-check-input"
                          id="exampleCheck1"
                        />
                        <label className="form-check-label diallogPo7Checkbox">
                          Save as private
                        </label>
                      </div>
                      <div className="form-group diallogPo7Button">
                        <a
                          href="Purchasing-Search-1.html"
                          className="btn btn-success"
                        >
                          Cancel
                        </a>
                        <a
                          href="#/"
                          data-toggle="modal"
                          data-target="#diallogPo8"
                          data-dismiss="modal"
                          className="btn btn-success"
                        >
                          Save
                        </a>
                      </div>
                    </form>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>

        {/* <!-- Dialog - PO  8 modal --> */}
        <section className="logout-modal">
          <div
            className="modal fade"
            id="diallogPo8"
            tabIndex="-1"
            role="dialog"
            aria-labelledby="exampleModalLabel"
            aria-hidden="true"
          >
            <div className="modal-dialog" role="document">
              <div className="modal-content">
                <div className="modal-body">
                  <button
                    type="button"
                    className="close"
                    data-dismiss="modal"
                    aria-label="Close"
                  >
                    <span aria-hidden="true">&times;</span>
                  </button>

                  <div className="modal-text-content text-center forgetPinModelContent">
                    <p>
                      "Greg's Custom Perchase View"
                      <br /> has been successfully added
                    </p>
                  </div>
                  <div className="text-center forgetPinSubmit">
                    <a href="#/">Okay</a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}
export default SearchOrderNotificatoins;
