import React, { Component } from "react";
import "../../../App.css";
import { Link } from "react-router-dom";

class OrderSeedTableBottomArea extends Component {
  render() {
    return (
      <div>
        <section className="button-area">
          <div className="container-fluid ">
            <div className="row to-button-area">
              <div className="col-xl-2 ">
                <div className="seeding-bottom-icon">
                  <Link
                    className="calendar-icon"
                    to="/seeding/order_seed_weeks"
                  >
                    <i className="fa fa-calendar"></i>
                  </Link>

                  <a href="#/">
                    <img src={require("../../../assets/icons/menu.svg")} />
                  </a>
                </div>
              </div>
              <div className="col-xl-2 ">
                <div className="to-custom-text-new2">
                  <p>
                    <span className="already-ordered"></span>Already Ordered
                  </p>
                </div>
              </div>
              <div className="col-xl-2">
                <div className="to-custom-text-new2">
                  <p>
                    <span className="should-ordered"></span>Should Order
                  </p>
                </div>
              </div>
              <div className="col-xl-2">
                <div className="to-custom-text-new2">
                  <p>
                    <span className="not-ordered"></span>Not Ordered
                  </p>
                </div>
              </div>
              <div className="col-xl-2">
                <div className="to-custom-text-new2">
                  <p>
                    <span className="back-ordered"></span>Not Ordered
                  </p>
                </div>
              </div>
              <div className="col-xl-2 text-left">
                <button
                  type="button"
                  className="btn  btn-lg to-seed-button-default-new "
                >
                  Order
                </button>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

export default OrderSeedTableBottomArea;
