import React, { Component } from "react";

class IncomingSeedTraysContent extends Component {
  state = {
    rowSelectedGreen: false,
    isStrikethroughVisible: false
  };

  tableRowColor = () => {
    this.setState({
      rowSelectedGreen: true
    });
  };

  tableRowColorNone = () => {
    this.setState({
      rowSelectedGreen: false
    });
  };
  strikethroughLine = () => {
    this.setState({
      isStrikethroughVisible: true
    });
  };
  render() {
    const { rowSelectedGreen } = this.state;
    const { isStrikethroughVisible } = this.state;
    return (
      <div>
        <div className="container-fluid no-container-padding">
          <div className="row">
            <div className="col-6">
              <div className="form-row to-filter-bg">
                <div className="form-group col-lg-6 ">
                  <div className="to-purchase-title-new">Sow Week</div>

                  <input
                    type="text"
                    className="form-control forced-margin"
                    id="inputSalesOrder"
                    placeholder="Week 1"
                  />
                </div>

                <div className="form-group col-lg-6">
                  <div className="to-purchase-title-new">Sow Date</div>

                  <input
                    type="text"
                    className="form-control"
                    id="inputDiscounts"
                    placeholder="MON"
                  />
                </div>
              </div>

              <div className="form-row DailyTaskTopBottomFilter2">
                <div className="col-4 form-group">Incoming Seed Trays</div>
                <div className="col-4 form-group seed-trays-text">
                  <p> Total # Trays : 14500</p>
                </div>
                <div className="col-4 form-group seed-trays-text">
                  <p>Total # of Plants : 150000</p>
                </div>
              </div>

              <table className="table to-seed-table-trays table-striped">
                <thead className="to-seed-thead-trays">
                  <tr>
                    <th>Species</th>
                    <th>Quantities</th>
                    <th>Tray Size</th>
                    <th># of Trays</th>
                    <th>Sow Date</th>
                    <th>House</th>
                    <th>Bay </th>
                    <th>Color </th>
                    <th>Bench(es) </th>
                  </tr>
                </thead>
                <tbody>
                  <tr
                    onClick={this.tableRowColor}
                    className={`${
                      rowSelectedGreen
                        ? "to-trays-table-row-color-green"
                        : "to-table-row"
                    } ${
                      isStrikethroughVisible ? "strikeout5 to-row-none" : ""
                    }`}
                  >
                    <td>Black Eyed Susan</td>
                    <td>50000</td>
                    <td>144</td>
                    <td>1048</td>
                    <td>05/01/2019</td>
                    <td>NA</td>
                    <td>NA</td>
                    <td>NA</td>
                    <td>NA</td>
                  </tr>
                  <tr>
                    <td>Pansy</td>
                    <td>75000</td>
                    <td>512</td>
                    <td>5000</td>
                    <td>05/01/2019</td>
                    <td>NA</td>
                    <td>NA</td>
                    <td>NA</td>
                    <td>NA</td>
                  </tr>
                  <tr>
                    <td>Black Eyed Susan</td>
                    <td>50000</td>
                    <td>144</td>
                    <td>1048</td>
                    <td>05/01/2019</td>
                    <td>NA</td>
                    <td>NA</td>
                    <td>NA</td>
                    <td>NA</td>
                  </tr>
                </tbody>
              </table>
            </div>
            <div className="col-6">
              <div className="form-row to-filter-bg-trays-right">
                <div className="form-group col-lg-6 ">
                  <div className="to-purchase-title-new">House</div>

                  <select
                    id="inputTerms"
                    className="form-control to-classic-seed"
                  >
                    <option defaultValue>DR1</option>
                    <option>...</option>
                  </select>
                </div>
              </div>

              <div className="form-row DailyTaskTopBottomFilter2">
                <div className="col-4 form-group">Available Spaces</div>
              </div>

              <table className="table to-seed-table-trays table-striped">
                <thead className="to-seed-thead-trays-right">
                  <tr>
                    <th>House</th>
                    <th>Bay</th>
                    <th>Color</th>
                    <th>Benches</th>
                    <th>Tray Space Available</th>
                  </tr>
                </thead>
                <tbody>
                  <tr
                    data-toggle="modal"
                    data-target="#FeedAssigning"
                    className={
                      rowSelectedGreen //new stuff
                        ? "to-table-row "
                        : "to-row-none"
                    }
                  >
                    <td>DR1</td>
                    <td>1N</td>
                    <td>Red</td>
                    <td>50</td>
                    <td>8500</td>
                  </tr>
                  <tr>
                    <td>DR1</td>
                    <td>4N</td>
                    <td>White</td>
                    <td>40</td>
                    <td>5000</td>
                  </tr>
                  <tr>
                    <td>DR1</td>
                    <td>4S</td>
                    <td>White</td>
                    <td>20</td>
                    <td>2000</td>
                  </tr>
                  <tr>
                    <td>DR1</td>
                    <td>11N</td>
                    <td>Blue</td>
                    <td>12</td>
                    <td>1250</td>
                  </tr>
                  <tr>
                    <td>DR1</td>
                    <td>13S</td>
                    <td>Red</td>
                    <td>6</td>
                    <td>600</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>

        <div className="container-fluild">
          <div className="SearchByMapBottomIndicators">
            <div className="row">
              <div className="col-6">
                <div className="SearchByMap-bottom-icon-2">
                  <a className="mb-2">
                    <img
                      className=""
                      src={require("../../../assets/icons/dashed-line.png")}
                    />
                    Exact
                  </a>
                  <br />
                  <a>
                    <img
                      src={require("../../../assets/icons/seeding-has-happened.png")}
                    />
                    Enough
                  </a>
                  <a>
                    <img
                      src={require("../../../assets/icons/not-enough.png")}
                    />
                    Not Enough
                  </a>
                  <a>
                    <img src={require("../../../assets/icons/Late.png")} /> Too
                    Much
                  </a>
                </div>
              </div>
              <div className="col-6 text-right">
                <div className="BottomButtons">
                  <button
                    onClick={this.OnShowClick}
                    type="button"
                    id="clickmark"
                    className="btn btn-lg to-button-default"
                  >
                    Assign
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
        {/* <!-- Dialog - Feed  Assigning--> */}
        <section className="logout-modal">
          <div
            className="modal fade"
            id="FeedAssigning"
            tabIndex="-1"
            role="dialog"
            aria-labelledby="exampleModalLabel"
            aria-hidden="true"
          >
            <div className="modal-dialog" role="document">
              <div className="modal-content SeedTraysAssign">
                <div className="modal-body">
                  <button
                    type="button"
                    className="close"
                    data-dismiss="modal"
                    aria-label="Close"
                  >
                    <span aria-hidden="true">&times;</span>
                  </button>

                  <div className="AddNewFeedContent">
                    <p>Assign To DR 1 Bay 1N Red?</p>
                  </div>
                  <div className="diallogPo7-form">
                    <form action="">
                      <div className="form-group">
                        <div className="row SeedTraysText">
                          <div className="col">
                            <p>1048 Needed</p>
                          </div>
                          <div className="col">
                            <p>8500 Available</p>
                          </div>
                          <div className="col">
                            <p>7452 Remaining</p>
                          </div>
                        </div>
                        <div className="row ">
                          <div className="col-6">
                            <div className="SeedTraysInputTitle">
                              <p> Select Bench(es) </p>

                              <select
                                id="inputTerms"
                                className="form-control to-classic-seed "
                              >
                                <option>8</option>
                                <option>9</option>
                                <option>10</option>
                                <option>11</option>
                                <option>12</option>
                                <option>13</option>
                                <option>14</option>
                                <option>15</option>
                              </select>
                            </div>
                          </div>
                        </div>

                        <div className="row ">
                          <div className="col-6">
                            <div className="SeedTraysInputTitle">
                              <p> Actual Qty </p>

                              <input
                                type="text"
                                className="form-control forced-margin "
                                id="inputSalesOrder"
                                placeholder="1048"
                              />
                            </div>
                          </div>
                          <div className="col-6">
                            <div className="SeedTraysInputTitle">
                              <ul className="list-inline DailyTaskTopCheckbox2">
                                <li className="list-inline-item">
                                  <label className="checkbox-inline no-label-margin">
                                    <input
                                      type="checkbox"
                                      defaultChecked
                                      value=""
                                    />
                                    Simulate Qty
                                  </label>
                                </li>
                              </ul>

                              <input
                                disabled
                                type="text"
                                className="form-control forced-margin input-disabled"
                                placeholder="Qty"
                              />
                            </div>
                          </div>
                        </div>
                      </div>

                      <div className="form-group text-center">
                        <a
                          data-dismiss="modal"
                          type="button"
                          className="btn seed-trays-dialog-btn"
                        >
                          NO
                        </a>
                        <a
                          onClick={() => {
                            this.strikethroughLine();
                            this.tableRowColorNone();
                          }}
                          data-toggle="modal"
                          data-dismiss="modal"
                          className="btn seed-trays-dialog-btn"
                        >
                          YES
                        </a>
                      </div>
                    </form>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

export default IncomingSeedTraysContent;
