import React, { Component } from "react";

class SowDetailsHeader extends Component {
  render() {
    return (
      <div>
        <div className="to-filter-bg">
          <div className="row">
            <div className="form-group col-lg-3">
              <div className="row">
                <div className="col to-select-margin">
                  <div className="to-purchase-title-2">Sow Week</div>

                  <select
                    id="inputTerms"
                    className="form-control to-classic-seed"
                  >
                    <option defaultValue>25/2019</option>
                    <option>...</option>
                    <option>...</option>
                  </select>
                </div>
                <div className="col to-select-margin">
                  <div className="to-purchase-title-2">Tray</div>

                  <select
                    id="inputTerms"
                    className="form-control to-classic-seed"
                  >
                    <option defaultValue>144</option>
                    <option>288</option>
                  </select>
                </div>
              </div>
            </div>

            <div className="col-lg-5 offset-lg-4  text-right">
              <div className="seeding-sow-space">
                <div className="input-group to-input-tb">
                  <input
                    className="form-control py-2"
                    placeholder="Add filter"
                  />
                  <span className="input-group-append">
                    <button
                      className="btn btn-outline-secondary to-filter-btn"
                      type="button"
                    >
                      <i className="fa fa-search to-icon-color"></i>
                    </button>
                  </span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default SowDetailsHeader;
