import React, { Component } from "react";
import Header from "../../components/Header";

import LogisticsSubModules from "./Components/LogisticSubModules";
import CopyRightsText from "../../components/CopyRightsText";
import LogicticsLogoArea from "./Components/LogicticsLogoArea";
import ChooseModule from "../../components/ChooseModule";

class LogisticsDashboard extends Component {
  componentDidMount() {
    document.title = "Logistics Dashboard";
  }
  render() {
    return (
      <div className="main-bg">
        <Header pageTitle="LOGISTICS Dashboard" />
        <div className="container">
          <LogicticsLogoArea />
          <ChooseModule />
          <LogisticsSubModules />

          <CopyRightsText />
        </div>
      </div>
    );
  }
}

export default LogisticsDashboard;
