import React, { Component } from "react";
import "../App.css";

import LogoArea from "../components/LogoArea";

import RecentUsedModule from "../components/RecentUsedModule";
import Header from "../components/Header";
import ChooseModule from "../components/ChooseModule";

class Dashboard extends Component {
  componentDidMount() {
    document.title = "Main Dashboard";
  }
  render() {
    return (
      <div className="main-bg">
        <Header pageTitle="Main Dashboard" headerTimer="to-show" />
        <div className="container">
          <LogoArea />
          <ChooseModule />

          <RecentUsedModule />
          <div className="row copyright">
            <div className="col-xl-12 text-center">
              <p className="copy-right-text">Copyright &copy; IDAPT 2019</p>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Dashboard;
