import React, { Component } from "react";

class ProductionLabelsForm extends Component {
  render() {
    return (
      <div>
        <div className="to-filter-bg">
          <form className="dropdownCenter">
            <div className="form-row ">
              <div className="form-group col-md-3 ">
                <label className="es_inputDropdownLebel">Ship Week</label>
                <input
                  type="text"
                  className="form-control"
                  placeholder="15/2019"
                />
              </div>
              <div className="form-group col-md-3">
                <label className="es_inputDropdownLebel">Sow Week</label>
                <input
                  type="text"
                  className="form-control"
                  placeholder="31/2019"
                />
              </div>
              <div className="form-group col-md-3">
                <label className="es_inputDropdownLebel">Batch</label>
                <input type="text" className="form-control" placeholder="1" />
              </div>
              <div className="form-group col-md-3">
                <label className="es_inputDropdownLebel">Item ID</label>
                <input
                  type="text"
                  className="form-control"
                  placeholder="223344"
                />
              </div>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

export default ProductionLabelsForm;
