import React, { Component } from "react";
import Header from "../../components/Header";
import BobsInternalFilter from "./Components/BobsInternalFilter";
import BobsInternalTable from "./Components/BobsInternalTable";
import BobsInternalButtons from "./Components/BobsInternalButtons";
import CopyRightsText from "../../components/CopyRightsText";
import BobsInternalNotifications from "./Components/BobsInternalNotifications";

class BobsInternalProduction extends Component {
  componentDidMount() {
    document.title = "Bobs Internal Production";
  }

  render() {
    return (
      <div>
        <Header pageTitle="BOBS INTERNAL PRODUCTION" />
        <BobsInternalFilter />
        <BobsInternalTable />
        <BobsInternalButtons />
        <BobsInternalNotifications />
        <CopyRightsText />
      </div>
    );
  }
}

export default BobsInternalProduction;
