import React, { Component } from "react";
import ProducitonLabelsHeader from "./Components/ProducitonLabelsHeader";
import ProductionLabelsForm from "./Components/ProductionLabelsForm";
import ProductionLabelsTable from "./Components/ProductionLabelsTable";
import CopyRightsText from "../../components/CopyRightsText";

class ProductionLabels extends Component {
  componentDidMount() {
    document.title = "Production Labels";
  }
  render() {
    return (
      <div>
        <ProducitonLabelsHeader pageTitle="Production Labels" />
        <ProductionLabelsForm />
        <ProductionLabelsTable />
        <CopyRightsText />
      </div>
    );
  }
}

export default ProductionLabels;
