import React, { Component } from "react";

class SearchPurchaseOrderHeader extends Component {
  state = {
    showSearchFilter: false,
    HideSearchFilter: true
  };

  OnShowClick = () => {
    this.setState({
      showSearchFilter: !this.state.showSearchFilter,
      HideSearchFilter: !this.state.HideSearchFilter
    });
    // this.setState({ HideSearchFilter: !this.state.HideSearchFilter });
  };
  render() {
    const { showSearchFilter } = this.state;
    const { HideSearchFilter } = this.state;
    return (
      <div>
        <div className="to-filter-bg">
          <div className="row">
            <div className="col-md-4 col-sm-12">
              {HideSearchFilter ? (
                <div className="to-filter-text">
                  Add filters from the search box
                </div>
              ) : null}

              {showSearchFilter ? (
                <div className="to-search-filter-to">
                  <a onClick={this.OnShowClick}>
                    x &nbsp; Search filter example
                  </a>
                </div>
              ) : null}
            </div>
            <div className="col-md-3 offset-md-5  text-right">
              <div className="input-group to-input-tb">
                <input
                  className="form-control py-2"
                  placeholder="Search filter example"
                />
                <span className="input-group-append">
                  <i
                    onClick={this.OnShowClick}
                    className="fa fa-search to-icon-color"
                  ></i>
                </span>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default SearchPurchaseOrderHeader;
