import React, { Component } from "react";
import Header from "../../components/Header";
import SearchFilterArea from "../../components/SearchFilterArea";
import ShipmentsTable from "./Components/ShipmentsTable";
import CopyRightsText from "../../components/CopyRightsText";

class Shipments extends Component {
  componentDidMount() {
    document.title = "Shipments";
  }
  render() {
    return (
      <div>
        <Header pageTitle="Shipments" />
        <SearchFilterArea placeholderText="Search Filter Area" />
        <ShipmentsTable />
        <CopyRightsText />
      </div>
    );
  }
}

export default Shipments;
