import React, { Component } from "react";
import Header from "../../components/Header";
import SearchFilterArea from "../../components/SearchFilterArea";
import PatchingListTable from "./Components/PatchingListTable";
import CopyRightsText from "../../components/CopyRightsText";
import "../../App.css";
import PatchingListNotifications from "./Components/PatchingListNotifications";

class PatchingList extends Component {
  componentDidMount() {
    document.title = "Patching List";
  }
  render() {
    return (
      <div>
        <Header pageTitle="Patching List" />
        <SearchFilterArea placeholderText="Search Filter Area" />
        <PatchingListTable />
        <PatchingListNotifications />
        <CopyRightsText />
      </div>
    );
  }
}

export default PatchingList;
