import React, { Component } from "react";
import "../App.css";

class InputForms extends Component {
  render() {
    return (
      <div>
        <form className="es_purchase-input to-custom-padding">
          <div className="form-row">
            <div className="form-group col-md-3">
              <label for="inputOrderDate" className="to-title-f-p-size">
                First Name
              </label>
              <input
                type="text"
                className="form-control to-input-f-b-c"
                id="inputEmail4"
                placeholder="Greg"
              />
            </div>
            <div className="form-group col-md-3">
              <label for="inputPurchaseOrder" className="to-title-f-p-size">
                Last Name
              </label>
              <input
                type="text"
                className="form-control to-input-f-b-c"
                id="inputPurchaseOrder"
                placeholder="Brewster"
              />
            </div>
            <div className="form-group col-md-3">
              <label for="inputVendor" className="to-title-f-p-size">
                Username
              </label>
              <input
                type="text"
                className="form-control to-input-f-b-c"
                id="inputVendor"
                placeholder="GBrewster"
              />
            </div>
            <div className="form-group col-md-3">
              <label for="inputCreator" className="to-title-f-p-size">
                Password
              </label>
              <input
                type="text"
                className="form-control to-input-f-b-c"
                id="inputVendor"
                placeholder="*****"
              />
            </div>
          </div>
          <div className="form-row">
            <div className="form-group col-md-3">
              <label for="inputOrderDate" className="to-title-f-p-size">
                Full Name
              </label>
              <input
                type="text"
                className="form-control to-input-f-b-c"
                id="inputEmail4"
                placeholder="Greg Brewster"
              />
            </div>
            <div className="form-group col-md-3">
              <label for="inputPurchaseOrder" className="to-title-f-p-size">
                Email
              </label>
              <input
                type="text"
                className="form-control to-input-f-b-c"
                id="inputPurchaseOrder"
                placeholder="gbrewster@idatp.com"
              />
            </div>
            <div className="form-group col-md-3">
              <label for="inputVendor" className="to-title-f-p-size">
                Mobile
              </label>
              <input
                type="text"
                className="form-control to-input-f-b-c"
                id="inputVendor"
                placeholder="304-123-4567"
              />
            </div>
            <div className="form-group col-md-3">
              <label for="inputCreator" className="to-title-f-p-size">
                Phone
              </label>
              <input
                type="text"
                className="form-control to-input-f-b-c"
                id="inputVendor"
                placeholder="304-123-4567"
              />
            </div>
          </div>

          <div className="form-row">
            <div className="form-group col-md-3">
              <label for="inputSalesOrder" className="to-title-f-p-size">
                Password Hint
              </label>
              <input
                type="text"
                className="form-control to-input-f-b-c"
                id="inputSalesOrder"
                placeholder="Hint"
              />
            </div>
            <div className="form-group col-md-3">
              <div className="row">
                <div className="col">
                  <label for="inputTerms" className="to-title-f-p-size">
                    {" "}
                    Timezone
                  </label>
                  <select
                    id="inputTerms"
                    className="form-control to-input-f-b-c to-classic"
                  >
                    <option defaultValue>EST</option>
                    <option>...</option>
                  </select>
                </div>
              </div>
            </div>

            <div className="form-group col-md-3">
              <label for="inputDiscounts" className="to-title-f-p-size">
                Country Code
              </label>
              <input
                type="text"
                className="form-control to-input-f-b-c"
                id="inputDiscounts"
                placeholder="Code"
              />
            </div>
            <div className="form-group col-md-3">
              <label for="inputDescriptionOrder" className="to-title-f-p-size">
                Comment
              </label>
              <input
                type="text"
                className="form-control to-input-f-b-c"
                id="inputDescriptionOrder"
                placeholder="Comment Here"
              />
            </div>
          </div>
        </form>
      </div>
    );
  }
}

export default InputForms;
