import React, { Component } from "react";
import Header from "../../components/Header";
import OrderSeedFilter from "./Components/OrderSeedFilter";
import OrderSeedTable from "./Components/OrderSeedTable";
import OrderSeedTableBottomArea from "./Components/OrderSeedTableBottomArea";
import CopyRightsText from "../../components/CopyRightsText";

class OrderSeedMainList extends Component {
  componentDidMount() {
    document.title = "Order Seed";
  }
  render() {
    return (
      <div>
        <Header pageTitle="Order Seed" />
        <OrderSeedFilter />
        <OrderSeedTable />
        <OrderSeedTableBottomArea />
        <CopyRightsText />
      </div>
    );
  }
}

export default OrderSeedMainList;
