import React, { Component } from "react";
import Header from "../../components/Header";
import SearchFilterArea from "../../components/SearchFilterArea";
import DemandTable from "./Components/DemandTable";
import CopyRightsText from "../../components/CopyRightsText";
import "../../App.css";
import CuttingsDemandNotifications from "./Components/CuttingsDemandNotifications";

class Demand extends Component {
  componentDidMount() {
    document.title = "Cuttings Demand";
  }
  render() {
    return (
      <div>
        <Header pageTitle="Demand" />
        <SearchFilterArea placeholderText="Search Filter Area" />
        <DemandTable />
        <CuttingsDemandNotifications />
        <CopyRightsText />
      </div>
    );
  }
}

export default Demand;
