import React, { Component } from "react";
import Header from "../../components/Header";
import SowSummaryHeader from "./Components/SowSummaryHeader";
import SowSummaryTable from "./Components/SowSummaryTable";
import CopyRightsText from "../../components/CopyRightsText";

class SowSummary extends Component {
  componentDidMount() {
    document.title = "Sow Summary";
  }
  render() {
    return (
      <div>
        <Header pageTitle="Sow Summary" />
        <SowSummaryHeader />
        <SowSummaryTable />
        <CopyRightsText />
      </div>
    );
  }
}

export default SowSummary;
