import React, { Component } from "react";
import { Link } from "react-router-dom";
import NotificationBoxIconsSlider from "../../../components/NotificationBoxIconsSlider";

class NewPurchaseReviewNotifications extends Component {
  state = {
    showNotification: false
  };

  OnShowNotification = () => {
    this.setState({ showNotification: !this.state.showNotification });
  };

  render() {
    const { showNotification } = this.state;

    return (
      <div>
        <div id="to-plusNotificationBar">
          <i onClick={this.OnShowNotification} className="fa fa-plus"></i>
        </div>
        {showNotification ? (
          <div className="to-Patching-list-data">
            <div className="container">
              <div className="row">
                <div className="col-6 d-flex justify-content-sm-start justify-content-xs-start">
                  <div className="chooseModuleHeader-to">
                    <p>Notification</p>
                  </div>
                </div>
                <div className="col-6 d-flex justify-content-sm-end justify-content-xs-end">
                  <div className="chooseModuleIcon-to ">
                    <ul>
                      <li>
                        <Link to="/dashboard">
                          <i className="fa fa-home"></i>
                        </Link>
                      </li>
                      <li className="iconModuleClose">
                        <i
                          onClick={this.OnShowNotification}
                          className="fa fa-times"
                        ></i>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>

              <NotificationBoxIconsSlider />

              <div className="row mt-3">
                <div className="col-sm-12 col-md-6">
                  <div className="to-Purchasing-action-menu-box">
                    <a data-toggle="modal" data-target="#dialogPo14">
                      Saved For <br /> Later
                    </a>
                  </div>
                </div>
                <div className="col-sm-12 col-md-6">
                  <div className="to-Purchasing-action-menu-box">
                    <a href="#/">
                      Save As <br /> Template
                    </a>
                  </div>
                </div>
              </div>

              <div className="row">
                <div className="col-12">
                  <div className="notification-box-padding-2x"></div>
                </div>
              </div>
            </div>
          </div>
        ) : null}

        {/* <!-- Dialog - PO  modal --> */}
        <section className="logout-modal">
          <div
            className="modal fade"
            id="dialogPo"
            tabIndex="-1"
            role="dialog"
            aria-labelledby="exampleModalLabel"
            aria-hidden="true"
          >
            <div className="modal-dialog" role="document">
              <div className="modal-content">
                <div className="modal-body">
                  <button
                    type="button"
                    className="close"
                    data-dismiss="modal"
                    aria-label="Close"
                  >
                    <span aria-hidden="true">&times;</span>
                  </button>

                  <div className="modal-text-content text-center forgetPinModelContent">
                    <p>
                      Purchase order #246813579 <br /> has been created
                    </p>
                  </div>
                  <div className="text-center diallogPo7Button">
                    {/* onclick="location.href='Purchasing-Search-1.html';" */}
                    <button
                      data-dismiss="modal"
                      type="button"
                      className="btn btn-success"
                    >
                      OK
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>

        {/* <!-- Dialog - PO  5 modal --> */}
        <section className="logout-modal">
          <div
            className="modal fade"
            id="dialogPo5"
            tabIndex="-1"
            role="dialog"
            aria-labelledby="exampleModalLabel"
            aria-hidden="true"
          >
            <div className="modal-dialog" role="document">
              <div className="modal-content">
                <div className="modal-body">
                  <button
                    type="button"
                    className="close"
                    data-dismiss="modal"
                    aria-label="Close"
                  >
                    <span aria-hidden="true">&times;</span>
                  </button>

                  <div className="modal-text-content diallogPo7Content">
                    <p>Email Order</p>
                  </div>
                  <div className="diallogPo7-form">
                    <form action="">
                      <div className="form-check">
                        <input
                          type="checkbox"
                          className="form-check-input"
                          id="exampleCheck1"
                        />
                        <label
                          className="form-check-label diallogPo7Checkbox"
                          htmlFor="exampleCheck1"
                        >
                          Add vendor
                        </label>
                      </div>
                      <div className="form-group mt-3">
                        <input
                          type="text"
                          className="form-control"
                          placeholder="dl@bobsmarket.com, vendor@acmecompany.com"
                        />
                      </div>
                      <div className="form-group diallogPo7Button">
                        <button
                          data-dismiss="modal"
                          type="button"
                          className="btn btn-success"
                        >
                          Cancel
                        </button>
                        <a
                          href="#/"
                          data-toggle="modal"
                          data-target="#dialogPo6"
                          data-dismiss="modal"
                          type="button"
                          className="btn btn-success"
                        >
                          Email
                        </a>
                      </div>
                    </form>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>

        {/* <!-- Dialog - PO  6 modal --> */}
        <section className="logout-modal">
          <div
            className="modal fade"
            id="dialogPo6"
            tabIndex="-1"
            role="dialog"
            aria-labelledby="exampleModalLabel"
            aria-hidden="true"
          >
            <div className="modal-dialog" role="document">
              <div className="modal-content">
                <div className="modal-body">
                  <button
                    type="button"
                    className="close"
                    data-dismiss="modal"
                    aria-label="Close"
                  >
                    <span aria-hidden="true">&times;</span>
                  </button>

                  <div className="modal-text-content text-center forgetPinModelContent">
                    <p>Email has been successfully sent</p>
                  </div>
                  <div className="form-group diallogPo7Button text-center">
                    {/* onclick="location.href='purchasing-dashboard.html';" */}
                    <button
                      data-dismiss="modal"
                      type="button"
                      className="btn btn-success"
                    >
                      OK
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>

        {/* <!-- Dialog - PO  14 modal --> */}
        <section className="logout-modal">
          <div
            className="modal fade"
            id="dialogPo14"
            tabIndex="-1"
            role="dialog"
            aria-labelledby="exampleModalLabel"
            aria-hidden="true"
          >
            <div className="modal-dialog" role="document">
              <div className="modal-content">
                <div className="modal-body">
                  <button
                    type="button"
                    className="close"
                    data-dismiss="modal"
                    aria-label="Close"
                  >
                    <span aria-hidden="true">&times;</span>
                  </button>

                  <div className="modal-text-content diallogPo7Content">
                    <p>Save For Later</p>
                  </div>
                  <div className="diallogPo7-form">
                    <form action="">
                      <div className="form-group mt-2">
                        <input
                          type="text"
                          className="form-control"
                          placeholder="07/05/2019"
                        />
                      </div>
                      <div className="form-group diallogPo7Button">
                        <button
                          data-dismiss="modal"
                          type="button"
                          className="btn btn-success"
                        >
                          Cancel
                        </button>
                        {/* onclick="location.href='purchasing-newPurchase-21.html';" */}
                        <button
                          data-dismiss="modal"
                          type="button"
                          className="btn btn-success"
                        >
                          Save
                        </button>
                      </div>
                    </form>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

export default NewPurchaseReviewNotifications;
