import React, { Component } from "react";

class SeedReceivingHeader extends Component {
  render() {
    return (
      <div>
        <div className=" to-seed-top-row-color">
          <div className="container-fluid">
            <form>
              <div className="form-row">
                <div className="form-group col-md-3">
                  <div className="to-purchase-title">Purchase Order #</div>

                  <input
                    type="text"
                    className="form-control"
                    id="inputSalesOrder"
                    placeholder="1234567890"
                  />
                </div>

                <div className="form-group col-md-3">
                  <div className="to-purchase-title">Date Range</div>

                  <input
                    type="text"
                    className="form-control"
                    id="inputDiscounts"
                    placeholder="06/10/2019"
                  />
                </div>

                <div className="form-group col-md-3 ">
                  <div className="to-purchase-title">Vendor</div>

                  <input
                    type="text"
                    className="form-control"
                    id="inputSalesOrder"
                    placeholder="Acme Company"
                  />
                </div>

                <div className="form-group col-md-3">
                  <div className="to-seed-button-area">
                    <a
                      data-toggle="modal"
                      data-target="#seed-manual-add"
                      className="top-seed-default seed-top-margin"
                      href="#/"
                    >
                      Manual Add
                    </a>
                  </div>
                </div>
              </div>
            </form>
          </div>
        </div>

        {/* <!-- Seed - Manual  Add modal --> */}
        <section className="logout-modal">
          <div
            className="modal fade"
            id="seed-manual-add"
            tabIndex="-1"
            role="dialog"
            aria-labelledby="exampleModalLabel"
            aria-hidden="true"
          >
            <div className="modal-dialog" role="document">
              <div className="modal-content">
                <div className="modal-body">
                  <button
                    type="button"
                    className="close"
                    data-dismiss="modal"
                    aria-label="Close"
                  >
                    <span aria-hidden="true">&times;</span>
                  </button>

                  <div className="modal-text-content diallogPo7Content">
                    <p>Manual Add</p>
                  </div>
                  <div className="diallogPo7-form">
                    <form action="">
                      <div className="form-group">
                        <input
                          type="text"
                          className="form-control"
                          placeholder="Client Name"
                        />
                      </div>
                      <div className="form-group">
                        <input
                          type="text"
                          className="form-control"
                          placeholder="Quantity"
                        />
                      </div>
                      <div className="form-group">
                        <input
                          type="text"
                          className="form-control"
                          placeholder="Sow Week"
                        />
                      </div>
                      <div className="form-group">
                        <input
                          type="text"
                          className="form-control"
                          placeholder="Ship Week"
                        />
                      </div>

                      <div className="form-group diallogPo7Button">
                        <button
                          type="button"
                          className="btn btn-success"
                          data-dismiss="modal"
                        >
                          Cancel
                        </button>
                        <button
                          type="button"
                          className="btn btn-success"
                          data-dismiss="modal"
                        >
                          Save
                        </button>
                      </div>
                    </form>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

export default SeedReceivingHeader;
