import React, { Component } from "react";
import Header from "../../components/Header";
import SearchByGridSearchFilterArea from "./Components/SearchByGridSearchFilterArea";
import SearchByGridArea from "./Components/SearchByGridArea";
import SearchByMapNotifications from "./Components/SearchByMapNotifications";
import CopyRightsText from "../../components/CopyRightsText";

class SearchByGrid extends Component {
  componentDidMount() {
    document.title = "Search By Grid";
  }
  render() {
    return (
      <div>
        <Header pageTitle="Search By Grid" />
        <SearchByGridSearchFilterArea />
        <SearchByGridArea />
        <SearchByMapNotifications />
        <CopyRightsText />
      </div>
    );
  }
}

export default SearchByGrid;
