import React, { Component } from "react";
import Header from "../../components/Header";
import AnotherFilter from "./Components/AnotherFilter";
import InternalSeedTable from "./Components/InternalSeedTable";
import CopyRightsText from "../../components/CopyRightsText";

class InternalSeedCatalog extends Component {
  componentDidMount() {
    document.title = "Internal Plug & Seed Program";
  }
  render() {
    return (
      <div>
        <Header pageTitle="Internal Plug & Seed Program" />
        <AnotherFilter />
        <InternalSeedTable />
        <CopyRightsText />
      </div>
    );
  }
}

export default InternalSeedCatalog;
