import React, { Component } from "react";
import Header from "../../components/Header";
import SeedingLogoArea from "./Components/SeedingLogoArea";
import SeedingChooseModule from "./Components/SeedingChooseModule";
import SeedingSubModules from "./Components/SeedingSubModules";
import CopyRightsText from "../../components/CopyRightsText";
import SeedingDashBoardNotifications from "./Components/SeedingDashBoardNotifications";

class SeedingDashboard extends Component {
  componentDidMount() {
    document.title = "Seeding Dashboard";
  }
  render() {
    return (
      <div className="main-bg">
        <Header pageTitle="Seeding Dashboard" />
        <div className="container">
          <SeedingLogoArea />
          <SeedingChooseModule />
          <SeedingSubModules />
          <SeedingDashBoardNotifications />
          <CopyRightsText />
        </div>
      </div>
    );
  }
}

export default SeedingDashboard;
