import React, { Component } from "react";

class AnotherFilter extends Component {
  render() {
    return (
      <div>
        <div className="to-filter-bg">
          <div className="row">
            <div className="col-lg-4 offset-lg-8  text-right">
              <div className="input-group to-input-tb">
                <input
                  className="form-control py-2"
                  placeholder="Another filter"
                />
                <span className="input-group-append">
                  <button
                    className="btn btn-outline-secondary to-filter-btn"
                    type="button"
                  >
                    <i className="fa fa-search to-icon-color"></i>
                  </button>
                </span>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default AnotherFilter;
