import React, { Component } from "react";
import "../../App.css";
import Header from "../../components/Header";
import BallWeeklyForm from "./Components/BallWeeklyForm";
import BallWeeklyTable from "./Components/BallWeeklyTable";
import BallWeeklyTableBottomArea from "./Components/BallWeeklyTableBottomArea";
import BallWeeklyGeneralSummary from "./Components/BallWeeklyGeneralSummary";
import BallWeeklyBottom from "./Components/BallWeeklyBottom";
import CopyRightsText from "../../components/CopyRightsText";

class BallWeeklyCallNotes extends Component {
  componentDidMount() {
    document.title = "Ball Weekly Call Notes";
  }
  render() {
    return (
      <div>
        <Header pageTitle="Ball Weekly Call Notes" />
        <div className="row">
          <div className="col-lg-9">
            <BallWeeklyForm />
            <BallWeeklyTable />
            <BallWeeklyTableBottomArea />
          </div>
          <div className="col-xl-3 to-filter-bg">
            <BallWeeklyGeneralSummary />
          </div>
        </div>
        <BallWeeklyBottom />
        <CopyRightsText />
      </div>
    );
  }
}

export default BallWeeklyCallNotes;
