import React, { Component } from "react";
import { Redirect } from "react-router-dom";
export default class SeedReceivingMainTable extends Component {
  state = {
    toDashboard: false
  };

  OnShowClick = () => {
    this.setState({
      toDashboard: true
    });
  };

  render() {
    if (this.state.toDashboard === true) {
      return <Redirect to="/seeding/seed_receiving_open_po" />;
    }
    return (
      <div>
        <div className="row">
          <div className="col-6">
            <div className="row to-seed-row-color">
              <div className="col-xl-6">Ordered</div>
              <div className="col-xl-6 d-flex justify-content-md-start justify-content-xl-end">
                Total Quantity Ordered : 0
              </div>
            </div>
            <div className=" table-responsive">
              <table className="table to-seed-table table-bordered">
                <col width="400" />
                <col width="400" />
                <col width="860" />

                <thead className="to-seed-thead">
                  <tr>
                    <th>PO#</th>
                    <th>Date</th>
                    <th>Vendor</th>
                  </tr>
                </thead>
                <tbody>
                  <tr
                    onClick={this.OnShowClick}
                    className="to-table-row to-seed-table-row-color-green"
                  >
                    <td> 12345667890</td>
                    <td>06/10/2019</td>
                    <td>Ball</td>
                  </tr>
                  <tr className="">
                    <td> 0987654321</td>
                    <td>06/10/2019</td>
                    <td>Ball</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
          <div className="col-6">
            <div className="row to-seed-row-color">
              <div className="col-xl-6">Ordered</div>
              <div className="col-xl-6 d-flex justify-content-md-start justify-content-xl-end">
                Total Quantity Ordered : 0
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
