import React, { Component } from "react";

class ReceiveCuttingsForm extends Component {
  render() {
    return (
      <div>
        <div className=" to-seed-top-row-color">
          <div className="container-fluid">
            <form>
              <div className="form-row">
                <div className="form-group col-md-3">
                  <div className="to-purchase-title">Purchase Order #</div>

                  <input
                    type="text"
                    className="form-control"
                    id="inputSalesOrder"
                    placeholder="1234567890"
                  />
                </div>

                <div className="form-group col-md-3">
                  <div className="to-purchase-title">Date Range</div>

                  <input
                    type="text"
                    className="form-control"
                    id="inputDiscounts"
                    placeholder="06/10/2019"
                  />
                </div>

                <div className="form-group col-md-3 ">
                  <div className="to-purchase-title">Vendor</div>

                  <input
                    type="text"
                    className="form-control"
                    id="inputSalesOrder"
                    placeholder="Acme Company"
                  />
                </div>

                <div className="form-group col-md-3">
                  <div className="to-seed-button-area">
                    <a
                      data-toggle="modal"
                      data-target="#seed-manual-add"
                      className="top-seed-default seed-top-margin"
                      href="#/"
                    >
                      Manual Add
                    </a>
                  </div>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    );
  }
}

export default ReceiveCuttingsForm;
