import React, { Component } from "react";

class DailyTaskTopForm extends Component {
  render() {
    return (
      <div>
        <div className="to-filter-bg">
          <div className="row">
            <div className="form-group col-lg-3">
              <div className="row">
                <div className="col to-select-margin2">
                  <select
                    id="inputTerms"
                    className="form-control to-input-f-b-c to-classNameic-seed "
                  >
                    <option defaultValue>DR1</option>
                    <option>...</option>
                  </select>
                </div>
              </div>
            </div>
            <div className="col-lg-9">
              <div className="row">
                <div className="col">
                  <ul className="list-inline DailyTaskTopList">
                    <li className="list-inline-item"># of Extras</li>
                    <li className="list-inline-item"># of Open Bays</li>
                    <li className="list-inline-item"># of Ships this week</li>
                    <li className="list-inline-item"># of Ships this week</li>
                    <li className="list-inline-item">
                      # of Patching this week
                    </li>
                    <li className="list-inline-item"># of Seeding</li>
                    <li className="list-inline-item">% full</li>
                  </ul>
                </div>
              </div>
            </div>
          </div>

          <div className="row pl-2 pb-2">
            <div className="col-9">
              <div className="row DailyTaskTopInput">
                <div className="col">
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Pansies"
                  />
                </div>
                <div className="col">
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Yellow"
                  />
                </div>
                <div className="col">
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Sow Week"
                  />
                </div>
                <div className="col">
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Sow Day"
                  />
                </div>
                <div className="col">
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Ship Week"
                  />
                </div>
                <div className="col">
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Ship Day"
                  />
                </div>
                <div className="col">
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Patch Week"
                  />
                </div>
                <div className="col">
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Patch Day"
                  />
                </div>
              </div>
            </div>
            <div className="col-3">
              <ul className="list-inline DailyTaskTopCheckbox">
                <li className="list-inline-item">
                  <label className="checkbox-inline">
                    <input
                      type="checkbox"
                      className="checkbox-1x"
                      defaultChecked
                      value=""
                    />
                    Seeding
                  </label>
                </li>
                <li className="list-inline-item">
                  <label className="checkbox-inline">
                    <input
                      type="checkbox"
                      className="checkbox-1x"
                      defaultChecked
                      value=""
                    />
                    Shipping
                  </label>
                </li>
                <li className="list-inline-item">
                  <label className="checkbox-inline">
                    <input
                      type="checkbox"
                      className="checkbox-1x"
                      defaultChecked
                      value=""
                    />
                    Patching
                  </label>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default DailyTaskTopForm;
