import React, { Component } from "react";
import Header from "../../components/Header";
import PatchingCombifixTopBar from "./Components/PatchingCombifixTopBar";
import PatchingCombifixContent from "./Components/PatchingCombifixContent";
import CopyRightsText from "../../components/CopyRightsText";

class PatchingCombifix extends Component {
  componentDidMount() {
    document.title = "Patching Combifix";
  }
  render() {
    return (
      <div>
        <Header pageTitle="Patching Combifix" />
        <PatchingCombifixTopBar patchingTitle="Patching Combifix" />
        <PatchingCombifixContent />
        <CopyRightsText />
      </div>
    );
  }
}

export default PatchingCombifix;
