import React, { Component } from "react";
import Header from "../../components/Header";
import PatchingLogoArea from "./Components/PatchingLogoArea";

import PatchingSubModule from "./Components/PatchingSubModule";
import CopyRightsText from "../../components/CopyRightsText";
import ChooseModule from "../../components/ChooseModule";

class PatchingDashboard extends Component {
  componentDidMount() {
    document.title = "Patching Dashboard";
  }
  render() {
    return (
      <div className="main-bg">
        <Header pageTitle="Patching Prototype" />
        <div className="container">
          <PatchingLogoArea />
          <ChooseModule />
          <PatchingSubModule />
          <CopyRightsText />
        </div>
      </div>
    );
  }
}

export default PatchingDashboard;
