import React, { Component } from "react";
import { Link } from "react-router-dom";
import "../../../App.css";
import NotificationBoxIconsSlider from "../../../components/NotificationBoxIconsSlider";
class BobsInternalNotifications extends Component {
  state = {
    showNotification: false
  };
  OnShowClick = () => {
    this.setState({ showNotification: !this.state.showNotification });
  };
  render() {
    const { showNotification } = this.state;
    return (
      <div>
        <div id="pur-dash-plusNotificationBar">
          <i onClick={this.OnShowClick} className="fa fa-plus"></i>
        </div>
        {showNotification ? (
          <div className="to-Patching-list-data">
            <div className="container">
              <div className="row">
                <div className="col-6 d-flex justify-content-sm-start justify-content-xs-start">
                  <div className="chooseModuleHeader-to">
                    <p>Notification</p>
                  </div>
                </div>
                <div className="col-6 d-flex justify-content-sm-end justify-content-xs-end">
                  <div className="chooseModuleIcon-to ">
                    <ul>
                      <li>
                        <Link to="/dashboard">
                          <i className="fa fa-home"></i>
                        </Link>
                      </li>
                      <li className="iconModuleClose">
                        <i
                          onClick={this.OnShowClick}
                          className="fa fa-times"
                        ></i>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>

              <NotificationBoxIconsSlider />

              <div className="row mt-3">
                <div className="col-sm-12 col-md-6">
                  <div className="to-Purchasing-action-menu-box">
                    <a
                      href="#/"
                      data-toggle="modal"
                      data-target="#seeding-dailog-po14"
                    >
                      Add <br /> Note
                    </a>
                  </div>
                </div>
                <div className="col-sm-12 col-md-6">
                  <div className="to-Purchasing-action-menu-box">
                    {/* href="bobs-internal-production-2.html" */}
                    <a href="#/">
                      Add <br /> New Line
                    </a>
                  </div>
                </div>
              </div>

              <div className="row">
                <div className="col-sm-12 col-md-6">
                  <div className="to-Purchasing-action-menu-box action-menu-bg">
                    {/* href="bobs-internal-production-2.html" */}
                    <a href="#/">
                      Highlight <br /> Selection
                    </a>
                  </div>
                </div>
              </div>
              <div className="row">
                <div className="col-sm-12">
                  <div className=" notification-box-padding-1x"></div>
                </div>
              </div>
            </div>
          </div>
        ) : null}

        {/* <!---Dialog PO 14--> */}
        <section className="logout-modal">
          <div
            className="modal fade"
            id="seeding-dailog-po14"
            tabIndex="-1"
            role="dialog"
            aria-labelledby="exampleModalLabel"
            aria-hidden="true"
          >
            <div className="modal-dialog" role="document">
              <div className="modal-content seeding-dialog23-modal-content">
                <div className="modal-body">
                  <button
                    type="button"
                    className="close"
                    data-dismiss="modal"
                    aria-label="Close"
                  >
                    <span aria-hidden="true">&times;</span>
                  </button>

                  <div className="text-white">
                    <p>Add Note</p>
                  </div>
                  <div className="modal-form-area forgetPinModalformArea text-center dialog23-form">
                    <form action="">
                      <textarea
                        style={{
                          width: "100%",
                          marginTop: "20px",
                          textAlign: "left"
                        }}
                        // style={{": 100%", "marginTop:": 20px text-align: left;"}}
                        name=""
                        id=""
                        cols="30"
                        rows="5"
                      >
                        6/12/19 Lorem ipsum, dolor sit amet consectetur
                        adipisicing elit. Vero ea quisquam ratione molestias
                        facilis debitis doloribus, temporibus dolore quae!
                      </textarea>
                    </form>
                  </div>

                  <div className="seeding-button-area">
                    <button
                      data-dismiss="modal"
                      href="#/"
                      className="seeding-dialog-btn"
                    >
                      Cancel
                    </button>
                    <button
                      href="bobs-internal-production.html"
                      className="seeding-dialog-btn"
                    >
                      Save
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

export default BobsInternalNotifications;
