import React, { Component } from "react";

class NewPurchaseHeader extends Component {
  render() {
    return (
      <div>
        <div className="container-fluid ">
          <form className="es_purchase-input">
            <div className="form-row">
              <div className="form-group col-md-3">
                <label className="to-title-f-p-size">Order Date</label>
                <input
                  type="date"
                  className="form-control to-input-f-b-c"
                  id="inputEmail4"
                  placeholder="04/10/2019"
                />
              </div>
              <div className="form-group col-md-3">
                <label className="to-title-f-p-size">Purchase Order #</label>
                <input
                  type="text"
                  className="form-control to-input-f-b-c"
                  id="inputPurchaseOrder"
                  placeholder="246813579"
                />
              </div>
              <div className="form-group col-md-3">
                <label className="to-title-f-p-size">Vendor</label>
                <input
                  type="text"
                  className="form-control to-input-f-b-c"
                  id="inputVendor"
                  placeholder="ACME COMPANY"
                />
              </div>
              <div className="form-group col-md-3">
                <label className="to-title-f-p-size">Creator</label>
                <input
                  type="text"
                  className="form-control to-input-f-b-c"
                  id="inputCreator"
                  placeholder="Greg Brewster"
                />
              </div>
            </div>

            <div className="form-row">
              <div className="form-group col-md-3">
                <label className="to-title-f-p-size">Sales Order #</label>
                <input
                  type="text"
                  className="form-control to-input-f-b-c"
                  id="inputSalesOrder"
                  placeholder="0987654321"
                />
              </div>
              <div className="form-group col-md-3">
                <div className="row">
                  <div className="col">
                    <label className="to-title-f-p-size">Terms </label>

                    <select
                      id="inputTerms"
                      className="form-control to-input-f-b-c to-classic"
                    >
                      <option defaultValue>Net 30</option>
                      <option>...</option>
                    </select>
                  </div>

                  <div className="col">
                    <label className="to-title-f-p-size">Dept</label>
                    <input
                      type="text"
                      className="form-control to-input-f-b-c"
                      id="inputDept"
                    />
                  </div>
                </div>
              </div>

              <div className="form-group col-md-3">
                <label className="to-title-f-p-size">Discounts</label>
                <input
                  type="text"
                  className="form-control to-input-f-b-c"
                  id="inputDiscounts"
                  placeholder="Discounts"
                />
              </div>
              <div className="form-group col-md-3">
                <label className="to-title-f-p-size">
                  Description of Order
                </label>
                <input
                  type="text"
                  className="form-control to-input-f-b-c"
                  id="inputDescriptionOrder"
                  placeholder="Order description"
                />
              </div>
            </div>
          </form>
        </div>
      </div>
    );
  }
}
export default NewPurchaseHeader;
