import React, { Component } from "react";
import "../../../App.css";
import { Link } from "react-router-dom";
class SpaceAllocationSubModules extends Component {
  render() {
    return (
      <div>
        <div className="row">
          <div className="col-xl-12">
            <div className="sub-module-title text-center">
              <h3>Greenhouse Space Allocation Modules</h3>
            </div>
          </div>
        </div>
        <div className="row recent-used-item">
          <div className="col-sm-3 ">
            <div className="subModule-item-box">
              <img src={require("../../../assets/icons/find.png")} alt="" />
              <span>
                <Link to="/space_allocation/search_by_map">Search by Map</Link>
              </span>
            </div>
          </div>
          <div className="col-sm-3 ">
            <div className="subModule-item-box">
              <img
                src={require("../../../assets/icons/space-menu.png")}
                alt=""
              />
              <span>
                <Link to="/space_allocation/search_by_grid">
                  Search by Grid
                </Link>
              </span>
            </div>
          </div>
          <div className="col-sm-3 ">
            <div className="subModule-item-box">
              <img src={require("../../../assets/icons/download.png")} alt="" />
              <span>
                <Link to="/space_allocation/incoming_seed_trays">
                  Incoming Seed Trays
                </Link>
              </span>
            </div>
          </div>
          <div className="col-sm-3">
            <div className="subModule-item-box">
              <img src={require("../../../assets/icons/plant.png")} alt="" />
              <span>
                <a data-toggle="modal" data-target="#PatchingNeeds" href="#/">
                  Patching Needs
                </a>
              </span>
            </div>
          </div>
        </div>
        <div className="row recent-used-item">
          <div className="col-sm-3 ">
            <div className="subModule-item-box">
              <img
                src={require("../../../assets/icons/logistics-delivery-truck-and-clock.png")}
                alt=""
              />
              <span>
                <Link to="/space_allocation/shipping_projection">
                  Shipping Projection
                </Link>
              </span>
            </div>
          </div>
          <div className="col-sm-3 ">
            <div className="subModule-item-box">
              <img src={require("../../../assets/icons/plant1.png")} alt="" />
              <span>
                <Link to="/space_allocation/extras">Extras</Link>
              </span>
            </div>
          </div>
          <div className="col-sm-3 ">
            <div className="subModule-item-box">
              <img src={require("../../../assets/icons/audit.png")} alt="" />
              <span>
                <a href="#/">Report</a>
              </span>
            </div>
          </div>
          <div className="col-sm-3 ">
            <div className="subModule-item-box">
              <img src={require("../../../assets/icons/audit.png")} alt="" />
              <span>
                <a href="#/">Report</a>
              </span>
            </div>
          </div>
        </div>

        {/* <!-- Dialog - Patching   Needs--> */}
        <section className="logout-modal">
          <div
            className="modal fade"
            id="PatchingNeeds"
            tabIndex="-1"
            role="dialog"
            aria-labelledby="exampleModalLabel"
            aria-hidden="true"
            data-backdrop="false"
          >
            <div className="modal-dialog" role="document">
              <div className="modal-content PatchingNeeds">
                <div className="modal-body">
                  <button
                    type="button"
                    className="close"
                    data-dismiss="modal"
                    aria-label="Close"
                  >
                    <span aria-hidden="true">&times;</span>
                  </button>

                  <div className="AddNewFeedContent">
                    <p>Patching Needs</p>
                  </div>
                  <div className="diallogPo7-form">
                    <form action="">
                      <div className="form-group">
                        <div className="row ">
                          <div className="col-12 text-center">
                            <div className="PatchingNeedsText">
                              <p> Edit Existing List or Create New? </p>
                            </div>
                          </div>
                        </div>
                      </div>

                      <div className="form-group text-center">
                        <a
                          data-toggle="modal"
                          data-target="#CreatePatchingList"
                          data-dismiss="modal"
                          type="button"
                          className="btn seed-trays-dialog-btn"
                        >
                          Create New
                        </a>
                        <Link
                          className="btn seed-trays-dialog-btn"
                          to="/space_allocation/patching_lists"
                        >
                          Edit Existing
                        </Link>
                      </div>
                    </form>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>

        {/* <!-- Dialog - Create  Patching --> */}
        <section className="logout-modal">
          <div
            className="modal fade"
            id="CreatePatchingList"
            tabIndex="-1"
            role="dialog"
            aria-labelledby="exampleModalLabel"
            aria-hidden="true"
            data-backdrop="false"
          >
            <div className="modal-dialog" role="document">
              <div className="modal-content CreatePatchingList">
                <div className="modal-body">
                  <button
                    type="button"
                    className="close"
                    data-dismiss="modal"
                    aria-label="Close"
                  >
                    <span aria-hidden="true">&times;</span>
                  </button>

                  <div className="AddNewFeedContent">
                    <p>Create New Patching Needs</p>
                  </div>
                  <div className="diallogPo7-form">
                    <form action="">
                      <div className="form-group">
                        <div className="row ">
                          <div className="col-12">
                            <div className="SeedTraysInputTitle">
                              <p> List Title </p>

                              <input
                                type="text"
                                className="form-control forced-margin "
                                placeholder="Patching List J"
                              />
                            </div>
                          </div>
                        </div>
                      </div>

                      <div className="form-group text-center">
                        <a
                          data-dismiss="modal"
                          type="button"
                          className="btn seed-trays-dialog-btn"
                        >
                          Cancel
                        </a>

                        <Link
                          className="btn seed-trays-dialog-btn"
                          to="/space_allocation/patching_needs"
                        >
                          Create
                        </Link>
                      </div>
                    </form>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

export default SpaceAllocationSubModules;
