import React, { Component } from "react";
import { Link } from "react-router-dom";
import NotificationBoxIconsSlider from "../../../components/NotificationBoxIconsSlider";

class SprayRecordsNotifications extends Component {
  state = {
    showNotification: false
  };
  OnShowClick = () => {
    this.setState({ showNotification: !this.state.showNotification });
  };
  render() {
    const { showNotification } = this.state;
    return (
      <div>
        <div id="plusNotificationBar">
          <i onClick={this.OnShowClick} className="fa fa-plus"></i>
        </div>

        {showNotification ? (
          <div className="to-Patching-list-data">
            <div className="container">
              <div className="row">
                <div className="col-6 d-flex justify-content-sm-start justify-content-xs-start">
                  <div className="chooseModuleHeader-to">
                    <p>Notification</p>
                  </div>
                </div>
                <div className="col-6 d-flex justify-content-sm-end justify-content-xs-end">
                  <div className="chooseModuleIcon-to ">
                    <ul>
                      <li>
                        <Link to="/dashboard">
                          <i className="fa fa-home"></i>
                        </Link>
                      </li>
                      <li className="iconModuleClose">
                        <i
                          onClick={this.OnShowClick}
                          className="fa fa-times"
                        ></i>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>

              <NotificationBoxIconsSlider />

              <div className="row mt-3">
                <div className="col-sm-12 col-md-6">
                  <div className="to-Purchasing-action-menu-box">
                    <br />
                    <Link to="/growing/spray">Edit</Link>
                  </div>
                </div>
                <div className="col-sm-12 col-md-6">
                  <div className="to-Purchasing-action-menu-box">
                    <a
                      href="#/"
                      data-toggle="modal"
                      data-target="#SprayRecordsViewReport"
                    >
                      View <br /> Report
                    </a>
                  </div>
                </div>
              </div>
              <div className="row">
                <div className="col-md-12">
                  <div className="notification-box-padding-2x"></div>
                </div>
              </div>
            </div>
          </div>
        ) : null}

        {/* <!-- Dialog - Feed  Assigning--> */}
        <section className="logout-modal">
          <div
            className="modal fade"
            id="SprayRecordsViewReport"
            tabIndex="-1"
            role="dialog"
            aria-labelledby="exampleModalLabel"
            aria-hidden="true"
          >
            <div className="modal-dialog" role="document">
              <div
                className="modal-content ViewReport {
"
              >
                <div className="modal-body">
                  <button
                    type="button"
                    className="close"
                    data-dismiss="modal"
                    aria-label="Close"
                  >
                    <span aria-hidden="true">&times;</span>
                  </button>

                  <div className="AddNewFeedContent">
                    <p>Record #</p>
                  </div>
                  <div className="diallogPo7-form">
                    <form action="">
                      <div className="form-group">
                        <div className="row">
                          <div className="col-12">
                            <div className="RecordBox text-center">
                              <p>Report</p>
                            </div>
                          </div>
                        </div>
                      </div>

                      <div className="form-group text-center">
                        <a
                          data-dismiss="modal"
                          type="button"
                          className="btn growing-dialog-record-btn"
                        >
                          Cancel
                        </a>

                        <Link
                          className="btn growing-dialog-record-btn"
                          to="/growing/spray"
                        >
                          Email
                        </Link>

                        <a
                          data-toggle="modal"
                          data-dismiss="modal"
                          className="btn growing-dialog-record-btn"
                        >
                          Print
                        </a>
                      </div>
                    </form>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}
export default SprayRecordsNotifications;
