import React, { Component } from "react";
import { Link } from "react-router-dom";
import NotificationBoxIconsSlider from "../../../components/NotificationBoxIconsSlider";

export default class SearchByMapNotifications extends Component {
  state = {
    showNotification: false
  };
  OnShowClick = () => {
    this.setState({ showNotification: !this.state.showNotification });
  };
  render() {
    const { showNotification } = this.state;
    return (
      <div>
        <div
          style={{ marginBottom: "-1.5rem" }}
          id="pur-dash-plusNotificationBar"
        >
          <i onClick={this.OnShowClick} className="fa fa-plus"></i>
        </div>

        {showNotification ? (
          <div className="to-Patching-list-data">
            <div className="container">
              <div className="row">
                <div className="col-6 d-flex justify-content-sm-start justify-content-xs-start">
                  <div className="chooseModuleHeader-to">
                    <p>Notification</p>
                  </div>
                </div>
                <div className="col-6 d-flex justify-content-sm-end justify-content-xs-end">
                  <div className="chooseModuleIcon-to ">
                    <ul>
                      <li>
                        <Link to="/dashboard">
                          <i className="fa fa-home"></i>
                        </Link>
                      </li>
                      <li className="iconModuleClose">
                        <i
                          onClick={this.OnShowClick}
                          className="fa fa-times"
                        ></i>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>

              <NotificationBoxIconsSlider />

              <div className="row mt-3">
                <div className="col-sm-12 col-md-6">
                  <div className="to-Purchasing-action-menu-box">
                    <a
                      href="#/"
                      data-toggle="modal"
                      data-target="#AddNewFeed"
                      data-dismiss="modal"
                    >
                      Add <br /> New
                    </a>
                  </div>
                </div>
                <div className="col-sm-12 col-md-6">
                  <div className="to-Purchasing-action-menu-box to-visibility-hide">
                    <a href="#/">
                      {" "}
                      See <br /> History
                    </a>
                  </div>
                </div>
              </div>
              <div className="row">
                <div className="col-sm-12 col-md-6">
                  <div className="to-Purchasing-action-menu-box to-visibility-hide ">
                    <a
                      href="#/"
                      data-toggle="modal"
                      data-target="#FeedAssigning"
                      data-dismiss="modal"
                    >
                      Assign <br /> Feeding
                    </a>
                  </div>
                </div>
                <div className="col-sm-12 col-md-6">
                  <div className="to-Purchasing-action-menu-box to-visibility-hide">
                    <a
                      href="#/"
                      data-toggle="modal"
                      data-target="#AddWeather"
                      data-dismiss="modal"
                    >
                      Weather <br /> Condition
                    </a>
                  </div>
                </div>
              </div>
              <div className="row">
                <div className="col-sm-12 ">
                  <div className=" notification-box-padding-1x"></div>
                </div>
              </div>
            </div>
          </div>
        ) : null}

        {/* <!-- Dialog - Feed  Assigning--> */}
        <section className="logout-modal">
          <div
            className="modal fade"
            id="SprayRecordsViewReport"
            tabIndex="-1"
            role="dialog"
            aria-labelledby="exampleModalLabel"
            aria-hidden="true"
          >
            <div className="modal-dialog" role="document">
              <div className="modal-content Notifications">
                <div className="modal-body">
                  <button
                    type="button"
                    className="close"
                    data-dismiss="modal"
                    aria-label="Close"
                  >
                    <span aria-hidden="true">&times;</span>
                  </button>

                  <div className="AddNewFeedContent">
                    <p>My Notification</p>
                  </div>
                  <div className="diallogPo7-form">
                    <form action="">
                      <div className="form-group"></div>

                      <div className="row">
                        <div className="col-sm-12 notification-p-text">
                          <p>Unread</p>
                        </div>
                      </div>

                      <div className="row ">
                        <div className="col-sm-9">
                          <div className="notitfification-content">
                            <div className="row">
                              <div className="col-sm-4 no-mod-context">
                                <div className="SearchByMap-notification-top">
                                  <a>
                                    <img
                                      src={require("../../../assets/icons/budding-leaves.png")}
                                    />{" "}
                                    Seeding
                                  </a>
                                </div>
                              </div>
                              <div className="col-sm-8">
                                <p className="SearchByMap-notification-top-text">
                                  Additional seed need for Lane 1. Reason:
                                  Dropped tray
                                </p>
                              </div>
                            </div>
                          </div>
                        </div>
                        <div className="col-sm-3 notification-dialog-button">
                          <button type="button" className="btn btn-success">
                            Open
                          </button>
                          <button
                            type="button"
                            className="btn btn-success notification-black-button"
                          >
                            Dismiss
                          </button>
                        </div>
                      </div>

                      <div className="row">
                        <div
                          style={{ marginTop: "3rem" }}
                          className="col-sm-12 notification-p-text"
                        >
                          <p>Archieve</p>
                        </div>
                      </div>
                    </form>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}
