import React, { Component } from "react";
import Header from "../../components/Header";
import { Link } from "react-router-dom";
import CopyRightsText from "../../components/CopyRightsText";

class ChemicalInventory extends Component {
  componentDidMount() {
    document.title = "Chemical Inventory";
  }
  render() {
    return (
      <div>
        <Header pageTitle="Chemical Inventory" />
        <div className="container-fluid">
          <div className="row">
            <div className="col-12">
              <div className="TrainingArea">
                <div className="TrainingButtons">
                  <button type="button" className="btn growing-training-btn">
                    <Link to="/growing/receive_from_po">Receive From PO</Link>
                  </button>

                  <button className="btn growing-training-btn">
                    <Link to="/growing/current_inventory">
                      View Current Inventory
                    </Link>
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
        <CopyRightsText />
      </div>
    );
  }
}

export default ChemicalInventory;
