import React, { Component } from "react";

class OrderSeedFilter extends Component {
  render() {
    return (
      <div>
        <div className="to-filter-bg">
          <div className="row">
            <div className="form-group col-lg-3">
              <div className="row">
                <div className="col to-select-margin2">
                  <select
                    id="inputTerms"
                    className="form-control to-input-f-b-c to-classNameic-seed "
                  >
                    <option defaultValue>Week(s)</option>
                    <option>Week 1</option>
                    <option>Week 2</option>
                    <option>Week 3</option>
                    <option>Week 4</option>
                    <option>Week 5</option>
                    <option>Week 6</option>
                    <option>Week 7</option>
                    <option>Week 8</option>
                    <option>Week 9</option>
                    <option>Week 10</option>
                    <option>Week 11</option>
                    <option>Week 12</option>
                    <option>Week 13</option>
                    <option>Week 14</option>
                  </select>
                </div>
              </div>
            </div>
            <div className="col-lg-4 col-sm-12">
              <div className="to-filter-text">
                Add filters from the search box
              </div>
            </div>
            <div className="col-lg-5 to-select-margin3  text-right">
              <div className="input-group to-input-tb">
                <input className="form-control py-2" placeholder="Add filter" />
                <span className="input-group-append">
                  <button
                    className="btn btn-outline-secondary to-filter-btn"
                    type="button"
                  >
                    <i className="fa fa-search to-icon-color"></i>
                  </button>
                </span>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default OrderSeedFilter;
