import React, { Component } from "react";

export default class InventoryAssignmentHeader extends Component {
  render() {
    return (
      <div>
        <form className="">
          <div className="form-row to-seed-top-row-color">
            <div className="form-group col-lg-3 ">
              <div className="to-purchase-title">Sow Week</div>

              <input
                type="text"
                className="form-control forced-margin"
                id="inputSalesOrder"
                placeholder="Week 3"
              />
            </div>

            <div className="form-group col-lg-3">
              <div className="to-purchase-title">Date</div>

              <input
                type="date"
                className="form-control"
                id="inputDiscounts"
                placeholder="06/10/2019"
              />
            </div>
            <div className="form-group col-lg-3">
              <div className="row">
                <div className="col">
                  <div className="to-purchase-title">Lane</div>

                  <select id="inputTerms" className="form-control to-classic">
                    <option defaultValue>Lane 1</option>
                    <option>Lane 2</option>
                    <option>Lane 3</option>
                    <option>Lane 4</option>
                    <option>Lane 5</option>
                    <option>Lane 5</option>
                    <option>All Lanes</option>
                  </select>
                </div>
              </div>
            </div>
          </div>
        </form>
      </div>
    );
  }
}
