import React, { Component } from "react";
import "../../../App.css";
class ClaimsFilter extends Component {
  render() {
    return (
      <div>
        <div className="to-filter-bg">
          <div className="row">
            <div className="col-lg-2 offset-lg-6 text-center">
              <div className="to-custom-text">
                <p>
                  <span className="to-green-box"></span>
                </p>
              </div>
            </div>

            <div className="col-lg-4   text-right">
              <div className="input-group to-input-tb">
                <input
                  className="form-control py-2"
                  placeholder="Another filter"
                />
                <span className="input-group-append">
                  <button
                    className="btn btn-outline-secondary to-filter-btn"
                    type="button"
                  >
                    <i className="fa fa-search to-icon-color"></i>
                  </button>
                </span>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default ClaimsFilter;
