import React, { Component } from "react";
import Header from "../../components/Header";
import SeedDailyTable from "./Components/SeedDailyTable";
import CopyRightsText from "../../components/CopyRightsText";

class SeedDaily extends Component {
  componentDidMount() {
    document.title = "Daily Seeding";
  }
  render() {
    return (
      <div>
        <Header pageTitle="Daily Seeding" />
        <SeedDailyTable />
        <CopyRightsText />
      </div>
    );
  }
}

export default SeedDaily;
