import React, { Component } from "react";
import Header from "../../components/Header";
import SeedProductionHeader from "./Components/SeedProductionHeader";
import SeedProductionTable from "./Components/SeedProductionTable";

class SeedProduciton extends Component {
  componentDidMount() {
    document.title = "Seed Production";
  }
  render() {
    return (
      <div>
        <Header pageTitle="Seed Production" />
        <SeedProductionHeader />
        <SeedProductionTable />
      </div>
    );
  }
}

export default SeedProduciton;
