import React, { Component } from "react";
import Header from "../../components/Header";
import SprayRecordsTable from "./Components/SprayRecordsTable";
import SprayRecordsNotifications from "./Components/SprayRecordsNotifications";

class SprayRecords extends Component {
  componentDidMount() {
    document.title = "Spray Records";
  }
  render() {
    return (
      <div>
        <Header pageTitle="Spray Records" />
        <SprayRecordsTable />
        <SprayRecordsNotifications />
      </div>
    );
  }
}

export default SprayRecords;
