import React, { Component } from "react";
import Header from "../../components/Header";
import NewPurchaseOrderContent from "./Components/NewPurchaseOrderContent";

class NewPurchaseOrder extends Component {
  componentDidMount() {
    document.title = "New Purchase Order";
  }
  render() {
    return (
      <div>
        <Header pageTitle="New Purchase Order" />
        <NewPurchaseOrderContent />
      </div>
    );
  }
}

export default NewPurchaseOrder;
