import React, { Component } from "react";
import Header from "../../components/Header";
import SearchFilterArea from "../../components/SearchFilterArea";
import LogisticsRoutingTable from "./Components/LogisticsRoutingTable";
import CopyRightsText from "../../components/CopyRightsText";

class LogisticsRouting extends Component {
  componentDidMount() {
    document.title = "Logistics & Routing";
  }
  render() {
    return (
      <div>
        <Header pageTitle="LOGISTICS & ROUTING" />
        <SearchFilterArea placeholderText="Search Filter Area" />
        <LogisticsRoutingTable />
        <CopyRightsText />
      </div>
    );
  }
}

export default LogisticsRouting;
