import React, { Component } from "react";
import { Link } from "react-router-dom";
class SeedingChooseModule extends Component {
  state = {
    showModules: false
  };
  OnShowClick = () => {
    this.setState({ showModules: !this.state.showModules });
  };
  render() {
    const { showModules } = this.state;
    return (
      <div>
        <div className="row">
          <div className="col-xl-6 offset-md-3 p-t-100">
            <form className="text-center">
              <div className="form-group new-form">
                <input
                  type="text"
                  list="browsers"
                  className="form-control"
                  id="autocomplete"
                  placeholder="Search"
                />
                <div className="search-menu-icon">
                  <a onClick={this.OnShowClick}>
                    <img
                      src={require("../../../assets/icons/menu.png")}
                      alt="menu"
                    />
                  </a>
                </div>
              </div>
            </form>
          </div>
        </div>
        {showModules ? (
          <section className="chooseModule">
            <div className="container">
              <div className="row">
                <div className="col-xl-6">
                  <div className="chooseModuleHeader">
                    <p>Choose a module</p>
                  </div>
                </div>
                <div className="col-xl-6">
                  <div className="chooseModuleIcon text-right">
                    <ul>
                      <li>
                        <a href="/#">
                          <img
                            src={require("../../../assets/icons/home-white.png")}
                            alt=""
                          />
                        </a>
                      </li>
                      <li
                        onClick={this.OnShowClick}
                        className="iconModuleClose"
                      >
                        <img
                          src={require("../../../assets/icons/exit-white.png")}
                          alt=""
                        />
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
              <div className="row">
                <div className="col-xl-3">
                  <div className="chooseModule-item-box">
                    <img
                      src={require("../../../assets/icons/tint.png")}
                      alt=""
                    />
                    <span>
                      <Link to="/seeding/order_seed_main_list">Order Seed</Link>
                    </span>
                  </div>
                </div>
                <div className="col-xl-3">
                  <div className="chooseModule-item-box">
                    <img
                      src={require("../../../assets/icons/tint.png")}
                      alt=""
                    />
                    <span>
                      <Link to="/seeding/seed_demand_main_list">
                        Seed Demand
                      </Link>
                    </span>
                  </div>
                </div>
                <div className="col-xl-3">
                  <div className="chooseModule-item-box">
                    <img
                      src={require("../../../assets/icons/tint.png")}
                      alt=""
                    />
                    <span>
                      <a href="#/">Seed Receiving</a>
                    </span>
                  </div>
                </div>
                <div className="col-xl-3">
                  <div className="chooseModule-item-box">
                    <img
                      src={require("../../../assets/icons/tint.png")}
                      alt=""
                    />
                    <span>
                      <a href="#/">Seed Production</a>
                    </span>
                  </div>
                </div>
              </div>
              <div className="row">
                <div className="col-xl-3">
                  <div className="chooseModule-item-box">
                    <img
                      src={require("../../../assets/icons/tint.png")}
                      alt=""
                    />
                    <span>
                      <a href="#/">Inventory Assignment</a>
                    </span>
                  </div>
                </div>
                <div className="col-xl-3">
                  <div className="chooseModule-item-box">
                    <img
                      src={require("../../../assets/icons/tint.png")}
                      alt=""
                    />
                    <span>
                      <a href="#/">Sow Details</a>
                    </span>
                  </div>
                </div>
                <div className="col-xl-3">
                  <div className="chooseModule-item-box">
                    <img
                      src={require("../../../assets/icons/tint.png")}
                      alt=""
                    />
                    <span>
                      <a href="#/">Sow Summary</a>
                    </span>
                  </div>
                </div>
                <div className="col-xl-3">
                  <div className="chooseModule-item-box">
                    <img
                      src={require("../../../assets/icons/tint.png")}
                      alt=""
                    />
                    <span>
                      {" "}
                      <Link to="/seeding/ball_weekly_call_notes">
                        Weekly Notes <br /> from Ball
                      </Link>
                    </span>
                  </div>
                </div>
              </div>
              <div className="row">
                <div className="col-xl-3">
                  <div className="chooseModule-item-box">
                    <img
                      src={require("../../../assets/icons/tint.png")}
                      alt=""
                    />
                    <span>
                      <Link to="/seeding/internal_seed_catalog">
                        Internal Seed Catalog
                      </Link>
                    </span>
                  </div>
                </div>
                <div className="col-xl-3">
                  <div className="chooseModule-item-box">
                    <img
                      src={require("../../../assets/icons/tint.png")}
                      alt=""
                    />
                    <span>
                      <Link to="/seeding/seed_daily">Daily Seeding</Link>
                    </span>
                  </div>
                </div>
                <div className="col-xl-3">
                  <div className="chooseModule-item-box">
                    <img
                      src={require("../../../assets/icons/tint.png")}
                      alt=""
                    />
                    <span>
                      <a href="#/">Seed Inventory</a>
                    </span>
                  </div>
                </div>
                <div className="col-xl-3">
                  <div className="chooseModule-item-box">
                    <img
                      src={require("../../../assets/icons/tint.png")}
                      alt=""
                    />
                    <span>
                      <a href="#/">Internal Production</a>
                    </span>
                  </div>
                </div>
              </div>
              <div className="row">
                <div className="col-xl-3">
                  <div className="chooseModule-item-box">
                    <img
                      src={require("../../../assets/icons/tint.png")}
                      alt=""
                    />
                    <span>
                      <Link to="/seeding/claims">Claims</Link>
                    </span>
                  </div>
                </div>
                <div className="col-xl-3">
                  <div className="chooseModule-item-box">
                    <img
                      src={require("../../../assets/icons/tint.png")}
                      alt=""
                    />
                    <span>
                      <a href="#/">Dirt Production</a>
                    </span>
                  </div>
                </div>
              </div>
            </div>
          </section>
        ) : null}
      </div>
    );
  }
}

export default SeedingChooseModule;
