import React, { Component } from "react";
import Header from "../../components/Header";
import SearchFilterArea from "../../components/SearchFilterArea";
import ShippingProjectionTable from "./Components/ShippingProjectionTable";
import CopyRightsText from "../../components/CopyRightsText";

class ShippingProjection extends Component {
  componentDidMount() {
    document.title = "Shipping Projection";
  }
  render() {
    return (
      <div>
        <Header pageTitle="Shipping Projection" />
        <SearchFilterArea placeholderText="Search Filter Area" />
        <ShippingProjectionTable />
        <CopyRightsText />
      </div>
    );
  }
}

export default ShippingProjection;
