import React, { Component } from "react";
import { Link } from "react-router-dom";
import "../../../App.css";
import NotificationBoxIconsSlider from "../../../components/NotificationBoxIconsSlider";
class SeedDemandMainListNotificatoins extends Component {
  state = {
    showNotification: false
  };
  OnShowClick = () => {
    this.setState({ showNotification: !this.state.showNotification });
  };
  handleClick = () => this.props.onClick(this.props.index);
  render() {
    const { showNotification } = this.state;
    return (
      <div>
        <div id="pur-dash-plusNotificationBar">
          <i onClick={this.OnShowClick} className="fa fa-plus"></i>
        </div>
        {showNotification ? (
          <div className="to-Patching-list-data">
            <div className="container">
              <div className="row">
                <div className="col-6 d-flex justify-content-sm-start justify-content-xs-start">
                  <div className="chooseModuleHeader-to">
                    <p>Notification</p>
                  </div>
                </div>
                <div className="col-6 d-flex justify-content-sm-end justify-content-xs-end">
                  <div className="chooseModuleIcon-to ">
                    <ul>
                      <li>
                        <Link to="/dashboard">
                          <i className="fa fa-home"></i>
                        </Link>
                      </li>
                      <li className="iconModuleClose">
                        <i
                          onClick={this.OnShowClick}
                          className="fa fa-times"
                        ></i>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>

              <NotificationBoxIconsSlider />

              <div className="row mt-3">
                <div className="col-sm-12 col-md-6">
                  <div className="to-Purchasing-action-menu-box">
                    <a href="/#">Import</a>
                  </div>
                </div>
                <div className="col-sm-12 col-md-6">
                  <div className="to-Purchasing-action-menu-box">
                    <a href="/#">Edit</a>
                  </div>
                </div>
              </div>

              <div className="row">
                <div className="col-sm-12 col-md-6">
                  <div className="to-Purchasing-action-menu-box">
                    <a
                      href="#/"
                      data-toggle="modal"
                      data-target="#CreateAmendment"
                    >
                      Add <br /> Ammendment
                    </a>
                  </div>
                </div>
                <div className="col-sm-12 col-md-6">
                  <div className="to-Purchasing-action-menu-box">
                    <a
                      href="#/"
                      data-toggle="modal"
                      data-target="#"
                      data-dismiss="modal"
                    >
                      Select Order <br /> Template
                    </a>
                  </div>
                </div>
              </div>
              <div className="row">
                <div className="col-12">
                  <div className=" notification-box-padding-1x"></div>
                </div>
              </div>
            </div>
          </div>
        ) : null}

        {/* <!-- Create Amendment --> */}
        <section className="logout-modal">
          <div
            className="modal fade"
            id="CreateAmendment"
            tabIndex="-1"
            role="dialog"
            aria-labelledby="exampleModalLabel"
            aria-hidden="true"
          >
            <div className="modal-dialog" role="document">
              <div className="modal-content CreateAmendment-modal-content">
                <div className="modal-body">
                  <button
                    type="button"
                    className="close"
                    data-dismiss="modal"
                    aria-label="Close"
                  >
                    <span aria-hidden="true">&times;</span>
                  </button>

                  <div className="text-white text-CreateAmendment">
                    <p>Create New Amendment</p>
                  </div>
                  <div className="CreateAmendmentForm">
                    <form action="">
                      <div className="container">
                        <div className="row">
                          <div className="col-xl-6">
                            <div className="form-group">
                              <input
                                type="text"
                                className="form-control"
                                placeholder="Internal Claim#"
                              />
                            </div>
                          </div>
                          <div className="col-xl-6">
                            <div className="form-group">
                              <input
                                type="text"
                                className="form-control"
                                placeholder="Lot #"
                              />
                            </div>
                          </div>
                        </div>
                        <div className="row">
                          <div className="col-xl-6">
                            <div className="form-group">
                              <input
                                type="text"
                                className="form-control"
                                placeholder="Species"
                              />
                            </div>
                          </div>
                          <div className="col-xl-6">
                            <div className="form-group">
                              <input
                                type="text"
                                className="form-control"
                                placeholder="Variety"
                              />
                            </div>
                          </div>
                        </div>
                        <div className="row">
                          <div className="col-xl-6">
                            <div className="form-group">
                              <input
                                type="text"
                                className="form-control"
                                placeholder="Color"
                              />
                            </div>
                          </div>
                          <div className="col-xl-6">
                            <div className="form-group">
                              <input
                                type="text"
                                className="form-control"
                                placeholder="Class"
                              />
                            </div>
                          </div>
                        </div>
                        <div className="row">
                          <div className="col-xl-12">
                            <textarea
                              placeholder="Description of issue"
                              name=""
                              className="form-control"
                              id=""
                              cols="30"
                              rows="3"
                            ></textarea>
                          </div>
                        </div>
                      </div>
                    </form>
                  </div>

                  <div className="seeding-button-area seeding-button-CreateAmendment">
                    <button className="seeding-dialog-btn">Cancel</button>
                    <button
                      data-toggle="modal"
                      data-target="#createClaimDialog2"
                      data-dismiss="modal"
                      className="seeding-dialog-btn"
                    >
                      Save
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>

        {/* <!-- Create Claim dialog 2 --> */}
        <section className="logout-modal">
          <div
            className="modal fade"
            id="createClaimDialog2"
            tabIndex="-1"
            role="dialog"
            aria-labelledby="exampleModalLabel"
            aria-hidden="true"
          >
            <div className="modal-dialog" role="document">
              <div className="modal-content seeding-dialog23-modal-content">
                <div className="modal-body">
                  <button
                    type="button"
                    className="close"
                    data-dismiss="modal"
                    aria-label="Close"
                  >
                    <span aria-hidden="true">&times;</span>
                  </button>

                  <div className="text-white">
                    <p>E-mail Seed Claim</p>
                  </div>
                  <br />
                  <div className="modal-form-area">
                    <div className="seeding-checkbox">
                      <div className="form-check form-check-inline">
                        <input
                          className="form-check-input"
                          type="checkbox"
                          id="inlineCheckbox1"
                          value="option1"
                        />
                        <label
                          className="form-check-label text-white"
                          for="inlineCheckbox1"
                        >
                          Add Vendor
                        </label>
                      </div>
                    </div>
                    <form action="">
                      <div className="form-group">
                        <input
                          type="text"
                          className="form-control"
                          placeholder="dl@boobsmarket.com, joe@vendor.com"
                        />
                      </div>
                    </form>
                  </div>

                  <div className="seeding-button-area">
                    <button className="seeding-dialog-btn">Cancel</button>
                    <button className="seeding-dialog-btn">Email</button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}
export default SeedDemandMainListNotificatoins;
