import React, { Component } from "react";
import { Link } from "react-router-dom";
import PostData from "../services/PostData";

import Logout from "./Logout";

class LoginArea extends Component {
  constructor(props) {
    super(props);
    this.state = {
      email: "",
      password: ""
    };

    this.login = this.login.bind(this);
    this.onChange = this.onChange.bind(this);
  }

  login() {
    PostData(this.state).then(result => {
      let responseJSON = result;
      const dummy = this.state;
      console.log(JSON.stringify(dummy));
      console.log(responseJSON);
    });
  }

  onChange(e) {
    this.setState({ [e.target.name]: e.target.value });
    console.log(this.state);
  }

  // ChangeScale = () => {
  //   this.setState({
  //     bgScale: false
  //   });
  // };
  render() {
    // const { bgScale } = this.state;
    return (
      <div>
        <section className="loginArea">
          <div className="container">
            <div className="row">
              <div className="col-xl-12">
                <div className="loginHeroImage text-center">
                  <img src={require("../assets/images/vbm_hero_img_2.png")} />
                </div>
              </div>
            </div>
            <div className="row">
              <div className="col-sm-12">
                <div className="loginForm">
                  <form action="">
                    <div className="form-group">
                      <input
                        name="email"
                        onClick={this.ChangeScale}
                        onChange={this.onChange}
                        type="text"
                        className="form-control"
                        placeholder="Employee ID"
                      />
                    </div>
                    <div className="form-group">
                      <input
                        name="password"
                        onChange={this.onChange}
                        type="password"
                        className="form-control"
                        id=""
                        placeholder="6 Digit Pin"
                      />
                    </div>
                  </form>
                  <p>Hint: This is your hint</p>
                  <div className="loginFormButtonLogin text-center">
                    <span>
                      <Link to="/dashboard">Login </Link>
                    </span>
                  </div>

                  <div className="loginFormButtonFoget text-center">
                    <span>
                      <Link
                        data-toggle="modal"
                        data-target="#forgetPin"
                        className="forgetPinButton"
                        to="/"
                      >
                        Forget PIN
                      </Link>
                    </span>
                  </div>
                </div>
              </div>
            </div>
            <div className="row">
              <div className="col-sm-12">
                <div className="loginPagefooterText text-center">
                  <p>v 1.0</p>
                  <p>Copyright &copy; IDAPT 2019</p>
                </div>
              </div>
            </div>
          </div>
        </section>
        <Logout />
      </div>
    );
  }
}

export default LoginArea;
