import React, { Component } from "react";

class InventoryAssignmentTable extends Component {
  state = {
    showNotification: false,
    isStrikethroughVisible: false
  };
  OnShowClick = () => {
    this.setState({ showNotification: !this.state.showNotification });
  };
  strikethroughLine = () => {
    this.setState({
      isStrikethroughVisible: true
    });
  };

  render() {
    const { showNotification } = this.state;
    const { isStrikethroughVisible } = this.state;
    return (
      <div>
        <div className="row">
          <div className="col-6">
            <div className="row to-seed-row-color">
              <div className="col-xl-6">Demand</div>
              <div className="col-xl-6 d-flex justify-content-md-start justify-content-xl-end">
                Total Quantity Ordered : 250000
              </div>
            </div>
            <div className=" table-responsive">
              <table className="table to-seed-table table-striped">
                <thead className="to-seed-thead">
                  <tr>
                    <td>
                      <input type="text" class="form-control" />
                    </td>
                    <td>
                      <input type="text" class="form-control" />
                    </td>
                    <td>
                      <input type="text" class="form-control" />
                    </td>
                    <td>
                      <input type="text" class="form-control" />
                    </td>
                    <td>
                      <input type="text" class="form-control" />
                    </td>
                    <td>
                      <input type="text" class="form-control" />
                    </td>
                  </tr>
                  <tr>
                    <th>Lane</th>
                    <th>Species</th>
                    <th>Lot#</th>
                    <th>Buffer</th>
                    <th>Multi Sown#</th>
                    <th>Destination</th>
                  </tr>
                </thead>
                <tbody>
                  <tr
                    className={
                      isStrikethroughVisible //new stuff
                        ? "strikeout4"
                        : ""
                    }
                  >
                    <td> 1</td>
                    <td>Macho Fern</td>
                    <td>123456</td>
                    <td>8</td>
                    <td>None</td>
                    <td>Green House 5</td>
                  </tr>
                  <tr className="">
                    <td> 1</td>
                    <td>Macho Fern</td>
                    <td>123456</td>
                    <td>8</td>
                    <td>None</td>
                    <td>Green House 5</td>
                  </tr>
                  <tr className="">
                    <td> 1</td>
                    <td>Macho Fern</td>
                    <td>123456</td>
                    <td>8</td>
                    <td>None</td>
                    <td>Green House 5</td>
                  </tr>
                  <tr className="">
                    <td> 1</td>
                    <td>Macho Fern</td>
                    <td>123456</td>
                    <td>8</td>
                    <td>None</td>
                    <td>Green House 5</td>
                  </tr>
                  <tr className="">
                    <td> 1</td>
                    <td>Macho Fern</td>
                    <td>123456</td>
                    <td>8</td>
                    <td>None</td>
                    <td>Green House 5</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>

          <div className="col-6">
            <div className="row to-seed-row-color">
              <div className="col-xl-6">Supplied</div>
              <div className="col-xl-6 d-flex justify-content-md-start justify-content-xl-end">
                Total Quantity Received : 250000
              </div>
            </div>
            <div className=" table-responsive">
              <table className="table to-seed-table">
                <thead className="to-seed-thead">
                  <tr>
                    <td>
                      <input type="text" class="form-control" />
                    </td>
                    <td>
                      <input type="text" class="form-control" />
                    </td>
                    <td>
                      <input type="text" class="form-control" />
                    </td>
                    <td>
                      <input type="text" class="form-control" />
                    </td>
                    <td>
                      <input type="text" class="form-control" />
                    </td>
                    <td>
                      <input type="text" class="form-control" />
                    </td>
                  </tr>
                  <tr>
                    <th>Lane</th>
                    <th>Species</th>
                    <th>Lot#</th>
                    <th>Buffer</th>
                    <th>Multi Sown#</th>
                    <th>Destination</th>
                  </tr>
                </thead>

                <tbody></tbody>
              </table>
            </div>
          </div>
        </div>
        <section className="button-area">
          <div className="container-fluid ">
            <div className="row">
              <div className="col-xl-2">
                <div className="to-custom-text">
                  <p>
                    <span className="scanned"></span>Exact
                  </p>
                  <p>
                    <span className="not-scanned"></span>Not Scanned
                  </p>
                </div>
              </div>
              <div className="col-xl-2">
                <div className="to-custom-text">
                  <p>
                    <span className="more-than-expected"></span>Too Much (3000)*
                  </p>
                  <p>
                    <span className="less-than-expected"></span>Not Enough
                    (Tol#)
                  </p>
                </div>
              </div>
              <div className="col-xl-2">
                <div className="to-custom-text">
                  <p>
                    <span className="return-box"></span>Return
                  </p>
                  <p>
                    <span className="strip-box"></span>Additional Seed Needed
                  </p>
                </div>
              </div>

              <div className="col-xl-6">
                <div className="BottomButtons">
                  <button
                    type="button"
                    className="btn btn-lg to-button-default"
                  >
                    Add Note
                  </button>
                  <button
                    onClick={this.OnShowClick}
                    type="button"
                    id="clickmark"
                    className="btn btn-lg to-button-default"
                  >
                    Scan
                  </button>
                </div>
              </div>
            </div>

            <div className="row ">
              <div className="col-xl-4">
                <p className="to-bottom-paragraph">
                  *5K overage accepted on lettuce, alyssum, herbs and raw snaps
                </p>
              </div>
              <div className="col-xl-4"></div>
            </div>
          </div>
        </section>

        {/* <!-- Notification modal --> */}
        {showNotification ? (
          <section
            onClick={() => {
              this.strikethroughLine();
              this.OnShowClick();
            }}
            className="to-seed-inventory-assignment-column-sort1 d-flex  align-items-center"
          >
            <div className="container">
              <div className="row">
                <div className="col-xl-12">
                  <div className="to-seed-inventory-assignment-column-sort1-element text-center">
                    <i className="fa fa-check"></i>
                    <p>Assigned to Lane 1</p>
                  </div>
                </div>
              </div>
            </div>
          </section>
        ) : null}
      </div>
    );
  }
}
export default InventoryAssignmentTable;
