import React, { Component } from "react";
import Header from "../../components/Header";
import PatchingCombifixTopBar from "./Components/PatchingCombifixTopBar";
import PatchingCombifixContent from "./Components/PatchingCombifixContent";
import CopyRightsText from "../../components/CopyRightsText";

class PatchingManual extends Component {
  componentDidMount() {
    document.title = "Patching Manual";
  }
  render() {
    return (
      <div>
        <Header pageTitle="Patching Manual" />
        <PatchingCombifixTopBar
          patchingTitle="Patching Manual"
          combifixbutton="to-hide"
        />
        <PatchingCombifixContent />
        <CopyRightsText />
      </div>
    );
  }
}

export default PatchingManual;
