import React, { Component } from "react";
import Header from "../../components/Header";
import GrowingLogoArea from "./Components/GrowingLogoArea";
import SeedingChooseModule from "../Seeding/Components/SeedingChooseModule";
import GrowingSubModules from "./Components/GrowingSubModules";

import CopyRightsText from "../../components/CopyRightsText";
import DailyTaskNotification from "./Components/DailyTaskNotification";

class GrowingDashboard extends Component {
  componentDidMount() {
    document.title = "Growing Dashboard";
  }
  render() {
    return (
      <div>
        <div className="main-bg">
          <Header pageTitle="Growing Dashboard" />

          <div className="container">
            <GrowingLogoArea />
            <SeedingChooseModule />
            <GrowingSubModules />
            <DailyTaskNotification />
            <CopyRightsText />
          </div>
        </div>
      </div>
    );
  }
}

export default GrowingDashboard;
