import React, { Component } from "react";
import Header from "../../components/Header";
import SearchByGridSearchFilterArea from "./Components/SearchByGridSearchFilterArea";
import PatchingNeedsArea from "./Components/PatchingNeedsArea";
import SearchByMapNotifications from "./Components/SearchByMapNotifications";

class PatchingNeeds extends Component {
  componentDidMount() {
    document.title = "Patching Needs";
  }
  render() {
    return (
      <div>
        <Header pageTitle="Patching Needs" />
        <SearchByGridSearchFilterArea />
        <PatchingNeedsArea />
        <SearchByMapNotifications />
      </div>
    );
  }
}

export default PatchingNeeds;
