import React, { Component } from "react";
import "../../../App.css";

class BallWeeklyTableBottomArea extends Component {
  render() {
    return (
      <div>
        <section>
          <div className="container-fluid ">
            <div className="row to-button-area">
              <div className="col-xl-2 ">
                <div className="seeding-bottom-icon">
                  <a href="#/">
                    <img
                      src={require("../../../assets/icons/icon_calendar.png")}
                    />
                  </a>
                  <a href="#/">
                    <img
                      src={require("../../../assets/icons/icon_listview.png")}
                    />
                  </a>
                </div>
              </div>
              <div className="col-xl-2 offset-xl-4">
                <div className="to-custom-text-new2">
                  <p>
                    <span className="already-ordered"></span>Already Ordered
                  </p>
                </div>
              </div>
              <div className="col-xl-2">
                <div className="to-custom-text-new2">
                  <p>
                    <span className="should-ordered"></span>Should Order
                  </p>
                </div>
              </div>
              <div className="col-xl-2">
                <div className="to-custom-text-new2">
                  <p>
                    <span className="not-ordered"></span>Not Ordered
                  </p>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

export default BallWeeklyTableBottomArea;
