import React, { Component } from "react";
import "../../App.css";
import Header from "../../components/Header";
import TransplantingForm from "./Components/TransplantingForm";
import CopyRightsText from "../../components/CopyRightsText";

class TransplantingPlannig extends Component {
  componentDidMount() {
    document.title = "Transplanting Planning";
  }
  render() {
    return (
      <div>
        <Header pageTitle="TRANSPLANTING PLANNING" />
        <TransplantingForm />
        <CopyRightsText />
      </div>
    );
  }
}

export default TransplantingPlannig;
